import { EventOfType, TypedCustomEvent } from "./TypedCustomEvent";
import { EventsFromTarget, TypedEventTarget } from "./TypedEventTarget";

export interface NextEventOptions<T> {
    predicate?: (value: T) => boolean;
    signal?: AbortSignal;
}

/**
 * Listen for the next event of some type, returning a Promise which resolves when the event is emitted.
 *
 * @param target Listen for the next event from this EventTarget
 * @param eventType Listen for this type of event.
 * @param options
 * @param options.signal An AbortSignal used to stop listening for the next event and reject the returned Promise.
 * @param options.predicate A function to be called with each event emitted by the target. It must return true before
 * the returned Promise is resolved (with event which satisfied the predicate).
 * @returns
 */
export const nextEvent = <
    Target extends TypedEventTarget,
    Events extends EventsFromTarget<Target>,
    EventType extends Events["type"]
>(
    target: Target,
    eventType: EventType,
    options?: NextEventOptions<EventOfType<EventType, Events>["detail"]>
) =>
    new Promise<EventOfType<EventType, Events>["detail"]>((resolve, reject) => {
        const predicate = options?.predicate ? options.predicate : () => true;
        const listener = ({ detail }: TypedCustomEvent) => {
            if (predicate(detail)) {
                resolve(detail);
                target.removeEventListener(eventType, listener);
            }
        };
        // We could pass the signal as part of AddEventListenerOptions here (which causes the EventTarget to
        // automatically clean up the listener if aborted), but since we have to listen to the abort event anyway to
        // reject the Promise, we'll just do our own cleanup there.
        target.addEventListener(eventType, listener);

        if (options?.signal) {
            options.signal.addEventListener(
                "abort",
                () => {
                    reject(new Error(`Promise aborted while waiting for next ${eventType} event.`));
                    target.removeEventListener(eventType, listener);
                },
                { once: true }
            );
        }
    });
