import { ConfigTargetingResponse, initConfigsClient, targetingQuery } from "@snapchat/configs-web";
import { cameraKitUserAgent } from "../common/cameraKitUserAgent";
import { Injectable } from "../dependency-injection/Injectable";
import { IndexedDBPersistence } from "../persistence/IndexedDBPersistence";
import { ensureError } from "../common/errors";
import { ChainableHandler, HandlerChainBuilder, RequestMetadata } from "./HandlerChainBuilder";
import { createRetryingHandler } from "./retryingHandler";
import { createTimeoutHandler } from "./timeoutHandler";
import { staleIfErrorCachingHandler } from "./staleIfErrorCachingHandler";
import {
    dispatchRequestCompleted,
    dispatchRequestErrored,
    dispatchRequestStarted,
} from "./requestStateEmittingHandler";

const doCofRequest: CofHandler = async ([cofRequest, cofMetadata]) => {
    const response = await targetingQuery(cofRequest, {
        "x-snap-client-user-agent": cameraKitUserAgent.userAgent,
        ...cofMetadata,
    });
    // NOTE: in order for cache persistance to work, we need to make the object cloneable,
    // i.e. with no methods (it appears targetingQuery() attaches toObject() to response object).
    delete response["toObject"];
    return response;
};

/**
 * Request emitting handler similar to createRequestStateEmittingHandler,
 * but which works with COF (given configs-web hides its transport).
 * TODO: once https://jira.sc-corp.net/browse/CAMKIT-2840 is in,
 * remove this handler and supply our default fetch handler chain to COF.
 */
const createCofRequestStateEmittingHandler = (): ChainableHandler<
    CofRequest,
    CofResponse,
    CofRequest,
    CofResponse,
    RequestMetadata
> => (next) => async (request, metadata) => {
    const dimensions = { requestType: COF_REQUEST_TYPE };
    const { requestId } = dispatchRequestStarted({ dimensions });
    try {
        const response = await next(request, metadata);
        // TODO: We hardcode status code and sizeByte values because we do not have access to underlying
        // transport of configs-web.
        // When this ticket is done https://jira.sc-corp.net/browse/CAMKIT-2840,
        // we will remove this handler and benefit from existing ones.
        const status = 200;
        let sizeByte = 0;
        try {
            sizeByte = new TextEncoder().encode(JSON.stringify(response)).byteLength;
        } finally {
            dispatchRequestCompleted({ requestId, dimensions, status, sizeByte });
            return response;
        }
    } catch (error) {
        dispatchRequestErrored({ requestId, dimensions, error: ensureError(error) });
        throw error;
    }
};

type CofRequest = Parameters<typeof targetingQuery>;
type CofResponse = ConfigTargetingResponse;

export const COF_REQUEST_TYPE = "cof";
export type CofDimensions = { requestType: typeof COF_REQUEST_TYPE };

export type CofHandler = (params: CofRequest) => Promise<CofResponse>;

/**
 * COF fetch client retries COF requests for 5 seconds and if unsuccessful it looks for previously stored response.
 *
 * @internal
 */
export const cofHandlerFactory = Injectable("cofHandler", () => {
    initConfigsClient();
    return new HandlerChainBuilder(doCofRequest)
        .map(createCofRequestStateEmittingHandler())
        .map(
            createRetryingHandler({
                // NOTE: targetingQuery() always converts failed responses into errors unlike fetch
                retryPredicate: (res) => res instanceof Error,
            })
        )
        .map(createTimeoutHandler({ timeout: 5 * 1000 }))
        .map(
            staleIfErrorCachingHandler({
                cache: new IndexedDBPersistence({ databaseName: "COFCache" }),
                keyResolver: (req) => JSON.stringify(req),
                // NOTE: we cache COF responses for 1 week for now
                maxAge: 7 * 24 * 60 * 60,
            })
        ).handler;
});
