function createBaseEncryptionData(): EncryptionData {
  return {
    key: new Uint8Array(),
    ivStore: {},
    isKeyServerEncrypted: false,
    isKeyE2eEncrypted: false,
    encryptionScheme: EncryptionData_EncryptionScheme.NOT_APPLICABLE,
  };
}

export const EncryptionData = {
  encode(message: EncryptionData, writer: Writer = Writer.create()): Writer {
    if (message.key.length !== 0) {
      writer.uint32(10).bytes(message.key);
    }
    Object.entries(message.ivStore).forEach(([key, value]) => {
      EncryptionData_IvStoreEntry.encode({ key: key as any, value }, writer.uint32(18).fork()).ldelim();
    });
    if (message.isKeyServerEncrypted === true) {
      writer.uint32(24).bool(message.isKeyServerEncrypted);
    }
    if (message.isKeyE2eEncrypted === true) {
      writer.uint32(32).bool(message.isKeyE2eEncrypted);
    }
    if (message.encryptionScheme !== EncryptionData_EncryptionScheme.NOT_APPLICABLE) {
      writer.uint32(40).int32(encryptionData_EncryptionSchemeToNumber(message.encryptionScheme));
    }
    return writer;
  },

  decode(input: Reader | Uint8Array, length?: number): EncryptionData {
    const reader = input instanceof Reader ? input : new Reader(input);
    let end = length === undefined ? reader.len : reader.pos + length;
    const message = createBaseEncryptionData();
    while (reader.pos < end) {
      const tag = reader.uint32();
      switch (tag >>> 3) {
        case 1:
          message.key = reader.bytes();
          break;
        case 2:
          const entry2 = EncryptionData_IvStoreEntry.decode(reader, reader.uint32());
          if (entry2.value !== undefined) {
            message.ivStore[entry2.key] = entry2.value;
          }
          break;
        case 3:
          message.isKeyServerEncrypted = reader.bool();
          break;
        case 4:
          message.isKeyE2eEncrypted = reader.bool();
          break;
        case 5:
          message.encryptionScheme = encryptionData_EncryptionSchemeFromJSON(reader.int32());
          break;
        default:
          reader.skipType(tag & 7);
          break;
      }
    }
    return message;
  },

  fromJSON(object: any): EncryptionData {
    return {
      key: isSet(object.key) ? bytesFromBase64(object.key) : new Uint8Array(),
      ivStore: isObject(object.ivStore)
        ? Object.entries(object.ivStore).reduce<{ [key: string]: Uint8Array }>((acc, [key, value]) => {
            acc[key] = bytesFromBase64(value as string);
            return acc;
          }, {})
        : {},
      isKeyServerEncrypted: isSet(object.isKeyServerEncrypted) ? Boolean(object.isKeyServerEncrypted) : false,
      isKeyE2eEncrypted: isSet(object.isKeyE2eEncrypted) ? Boolean(object.isKeyE2eEncrypted) : false,
      encryptionScheme: isSet(object.encryptionScheme)
        ? encryptionData_EncryptionSchemeFromJSON(object.encryptionScheme)
        : EncryptionData_EncryptionScheme.NOT_APPLICABLE,
    };
  },

  toJSON(message: EncryptionData): unknown {
    const obj: any = {};
    message.key !== undefined &&
      (obj.key = base64FromBytes(message.key !== undefined ? message.key : new Uint8Array()));
    obj.ivStore = {};
    if (message.ivStore) {
      Object.entries(message.ivStore).forEach(([k, v]) => {
        obj.ivStore[k] = base64FromBytes(v);
      });
    }
    message.isKeyServerEncrypted !== undefined && (obj.isKeyServerEncrypted = message.isKeyServerEncrypted);
    message.isKeyE2eEncrypted !== undefined && (obj.isKeyE2eEncrypted = message.isKeyE2eEncrypted);
    message.encryptionScheme !== undefined &&
      (obj.encryptionScheme = encryptionData_EncryptionSchemeToJSON(message.encryptionScheme));
    return obj;
  },

  fromPartial<I extends Exact<DeepPartial<EncryptionData>, I>>(object: I): EncryptionData {
    const message = createBaseEncryptionData();
    message.key = object.key ?? new Uint8Array();
    message.ivStore = Object.entries(object.ivStore ?? {}).reduce<{ [key: string]: Uint8Array }>(
      (acc, [key, value]) => {
        if (value !== undefined) {
          acc[key] = value;
        }
        return acc;
      },
      {}
    );
    message.isKeyServerEncrypted = object.isKeyServerEncrypted ?? false;
    message.isKeyE2eEncrypted = object.isKeyE2eEncrypted ?? false;
    message.encryptionScheme = object.encryptionScheme ?? EncryptionData_EncryptionScheme.NOT_APPLICABLE;
    return message;
  },
};

