import { Injectable } from "../dependency-injection/Injectable";
import { LensProto } from "../lens/Lens";

/**
 * Holds information about a lens being rendered.
 */
export interface LensContext {
    activeLens: LensProto | undefined;
}

/**
 * @internal
 */
export const lensContextFactory = Injectable("LensContext", () => {
    const lensContext: LensContext = { activeLens: undefined };
    return lensContext;
});
