import { guard, validate } from "../common/validate";
import { isSafeString, isSafeStringArray } from "../common/typeguards";
import { Injectable } from "../dependency-injection/Injectable";
import { cameraKitServiceFetchHandlerFactory } from "../handlers/cameraKitServiceFetchHandlerFactory";
import { FetchHandler } from "../handlers/defaultFetchHandler";
import { LensSources, lensSourcesFactory } from "../extensions/LensSources";
import { Lens, LensProto, toPublicLens } from "./Lens";
import { retrieveCameraKitLens, retrieveCameraKitLensGroup } from "./lensHttpUtil";
import { decodeEnvelopes } from "./lensEnvelopeUtil";

export interface LensGroupsQueryResponse {
    errors: Error[];
    lenses: Lens[];
}

/**
 * The LensRepository is used to query for lenses from specific lens groups, or for a lens with a specific ID.
 *
 * **Note on caching:** currently, LensRepository relies on the remote Lens service to set appropriate Cache-Control
 * headers -- that is, caching lens metadata is assumed to be handled by the browser. If we cannot always guarantee
 * correct Cache-Control headers (or need more control over caching in general), we will need to add some kind of
 * caching handler to the handler chained used by the LensRepository.
 *
 * @internal
 */
export class LensRepository {
    private readonly lensCache = new Map<string, LensProto>();

    constructor(private readonly httpClient: FetchHandler, private readonly lensSources: LensSources) {}

    /**
     * Retrieve a single Lens.
     *
     * @param lensId Desired Lens's unique ID. Can be found in the CameraKit Portal.
     * @param groupId The ID of a group containing the desired Lens. Can be found in the CameraKit Portal.
     * @returns Resolves with the desired Lens, or rejects if an error occurred (including a missing Lens).
     */
    @validate
    async loadLens(@guard(isSafeString) lensId: string, @guard(isSafeString) groupId: string): Promise<Lens> {
        const envelopes = await this.lensSources.retrieveLenses({ lensId, groupId });
        let lens: LensProto;
        if (envelopes) {
            lens = decodeEnvelopes(envelopes)[0];
            if (!lens) {
                throw new Error("Expected non-empty envelope");
            }
        } else {
            lens = await retrieveCameraKitLens(this.httpClient, lensId, groupId);
        }
        this.lensCache.set(lens.id, lens);
        return toPublicLens(lens);
    }

    /**
     * Retrieve the Lenses contained in a list of Lens Groups.
     *
     * This may result in multiple requests to retrieve Lens data (e.g. one per desired group). If any constituent
     * requests fail, those errors will be reported in the response  but the returned Promise will not be rejected. Any
     * Lenses which could be successfully retrieved will be availble in the response.
     *
     * @param groupIds A list of Lens Group IDs. Can be found in the CameraKit Portal.
     * @returns Resolves with a flattened list of all lenses in the desired groups. If any errors occurred during the
     * query operation, these will be included in a separate list. If errors are present, the list of Lenses may not
     * contain all the Lenses from the desired groups.
     */
    @validate
    async loadLensGroups(@guard(isSafeStringArray) groupIds: string[]): Promise<LensGroupsQueryResponse> {
        const responses = await Promise.all(
            groupIds.map(async (groupId) => {
                try {
                    const envelopes = await this.lensSources.retrieveLenses({ groupId });
                    const lenses = envelopes
                        ? decodeEnvelopes(envelopes)
                        : await retrieveCameraKitLensGroup(this.httpClient, groupId);
                    lenses.forEach((lens) => this.lensCache.set(lens.id, lens));
                    return lenses.map(toPublicLens);
                } catch (error) {
                    return error as Error;
                }
            })
        );

        const errors: Error[] = [];
        const lenses: Lens[] = [];
        responses.forEach((response) => (Array.isArray(response) ? lenses.push(...response) : errors.push(response)));
        return { errors, lenses };
    }

    /**
     * Returns cached Lens proto if available.
     * @param lensId Lens ID.
     * @returns Lens proto or undefined.
     */
    getCachedLens(lensId: string): LensProto | undefined {
        return this.lensCache.get(lensId);
    }
}

/**
 * @internal
 */
export const lensRepositoryFactory = Injectable(
    "LensRepository",
    [cameraKitServiceFetchHandlerFactory.token, lensSourcesFactory.token] as const,
    (fetchHandler: FetchHandler, lensSources: LensSources) => new LensRepository(fetchHandler, lensSources)
);
