const logMethods = ["log", "warn", "info", "debug", "error"] as const;

type Logger = Record<typeof logMethods[number], (message?: any, ...optionalParams: any[]) => void>;

/**
 * Logger that does nothing.
 */
const noopLogger: Logger = {
    log: () => {},
    warn: () => {},
    info: () => {},
    debug: () => {},
    error: () => {},
};

/**
 * Logger that prints to console.
 */
const consoleLogger: Logger = {
    log: (...args) => console.log(...args),
    warn: (...args) => console.warn(...args),
    info: (...args) => console.info(...args),
    debug: (...args) => console.debug(...args),
    error: (...args) => console.error(...args),
};

/**
 * Current logger instance.
 */
let logger = noopLogger;

const loggerMap = {
    noop: noopLogger,
    console: consoleLogger,
};

/**
 * Sets current logger instance.
 *
 * @internal
 */
export function setLogger(target: keyof typeof loggerMap) {
    logger = loggerMap[target] ?? noopLogger;
}

/**
 * Gets logger for given module.
 *
 * @internal
 *
 * @param module Module name.
 * @returns Logger instance.
 */
export function getLogger(module: string): Logger {
    const prefix = `[CameraKit.${module}]`;
    const scopedLogger = { ...logger };
    for (const logMethod of logMethods) {
        scopedLogger[logMethod] = (...args: any[]) => logger[logMethod](prefix, ...args);
    }
    return scopedLogger;
}
