import { Injectable } from "../../dependency-injection/Injectable";
import { nextEvent, NextEventOptions } from "../../events/nextEvent";
import { Lens } from "../../lens";
import { TypedCustomEvent } from "../../events/TypedCustomEvent";
import { SessionStateEventTarget, sessionStateEventTargetFactory } from "../../session/sessionStateEventTarget";
import { MetricsEventTarget, metricsEventTargetFactory } from "../metricsEventTarget";
import { CameraKitSession, cameraKitSessionFactory } from "../../session/CameraKitSession";
import { MakeTaggedBusinessEvent } from "../businessEventsReporter";
import { getTimeMs } from "../../common/time";
import { operationalMetricReporterFactory, OperationalMetricsReporter } from "../operationalMetricsReporter";
import { CameraKitConfiguration, configurationToken } from "../../configuration";

// We ignore short-duration lens views.
//
// The value is documented here:
// https://docs.google.com/document/d/1-kSzFWCWw9Qo3D08FR1_cqeHTsUtk9p3p3uOptzWDTY/edit#heading=h.q5liip76r9lt
const viewTimeThresholdSec = 0.1;

/**
 * The LensView metric is emitted after a lens has been viewed (for longer than 100ms), when the lens is turned off.
 *
 * It contains information about rendering performance.
 *
 * It corresponds to the internal CameraKitLensSwipe event, described here:
 * https://docs.google.com/document/d/1-kSzFWCWw9Qo3D08FR1_cqeHTsUtk9p3p3uOptzWDTY#heading=h.q5liip76r9lt
 *
 * Notes:
 *   - If the page is hidden (e.g. user switches to a different tab, or application, or closes the tab, or closes the
 *     browser, navigates to a new page, refreshes, etc.) this metric will be emitted at that time. This is to ensure
 *     we don't lose the metric if the page is closed.
 *   - If the page is hidden and then made visible again later (e.g. user switches to a different tab, then back), we
 *     will begin measuring a new LensView. That is, we will not capture the time when the page is hidden even if the
 *     lens is still rendering in the background.
 */
export type LensView = MakeTaggedBusinessEvent<"lensView">;

/**
 * @internal
 */
export const reportLensView = Injectable(
    "reportLensView",
    [
        cameraKitSessionFactory.token,
        sessionStateEventTargetFactory.token,
        metricsEventTargetFactory.token,
        operationalMetricReporterFactory.token,
        configurationToken,
    ] as const,
    (
        session: CameraKitSession,
        stateTarget: SessionStateEventTarget,
        metricsEventTarget: MetricsEventTarget,
        operationalMetricsReporter: OperationalMetricsReporter,
        configuration: CameraKitConfiguration
    ) => {
        let abortController = new AbortController();

        const maybeDispatchLensView = async ({
            applyDelaySec,
            lensId,
            viewTimeSec,
        }: Pick<LensView, "applyDelaySec" | "lensId" | "viewTimeSec">) => {
            if (viewTimeSec < viewTimeThresholdSec) return;
            const { avgFps, lensFrameProcessingTimeMsAvg, lensFrameProcessingTimeMsStd } = session.metrics.frames;
            const { cluster: performanceCluster, webglRendererInfo } = (await configuration.lensPerformance) ?? {
                cluster: -1,
                webglRendererInfo: "unknown",
            };
            const lensView: LensView = {
                name: "lensView",
                applyDelaySec,
                avgFps,
                lensId,
                lensFrameProcessingTimeMsAvg,
                lensFrameProcessingTimeMsStd,
                // We don't support recording video, but applications may do this without our knowledge.
                recordingTimeSec: 0,
                viewTimeSec,
                // TODO: if we want to support these fields, we'll need some persistence to keep track of the date
                // of last application per lens.
                // https://jira.sc-corp.net/browse/CAMKIT-3050
                isLensFirstWithinDay: false,
                isLensFirstWithinMonth: false,
                performanceCluster,
                webglRendererInfo,
            };

            metricsEventTarget.dispatchEvent(new TypedCustomEvent("lensView", lensView));

            // Reporting operational metrics described here:
            // https://docs.google.com/document/d/1g4PncAmKdyrLEFVp8ODp58oer2UJu9PIetRW035nnTo
            //
            // We ignore if applyDelaySec is 0, which will be the case if the session was suspended and then resumed 
            // we only care about this metric when a new lens has been loaded.
            if (applyDelaySec > 0) operationalMetricsReporter.timer("lens.core_loading_latency", applyDelaySec * 1000);
            operationalMetricsReporter.timer(
                "lens.processing_time",
                lensFrameProcessingTimeMsAvg,
                new Map([["performance_cluster", performanceCluster.toString()]])
            );
        };

        const measureLensView = async (
            lensId: string,
            lensLoadedTime: Promise<number>,
            lensTurnedOnTime: Promise<number>,
            lensTurnedOffTime: Promise<number>
        ) => {
            const lensInitiatedTime = getTimeMs();
            const applyDelay = lensLoadedTime.then((lensLoadedTimeMs) => (lensLoadedTimeMs - lensInitiatedTime) / 1000);
            let hasSuspended = false;

            // Once the lens has turned on, we also need to listen for the sessionSuspended event (which occurs if the
            // page is hidden) and treat as if we had gotten lensTurnedOff at that time.
            //
            // Note: we can't turn the sessionSuspended event into a Promise because if sessionSuspended comes as the
            // result of page termination, we can't do anything async (like construct a Promise)  if we do, the browser
            // will simply terminate the page and never resolve the Promise.
            Promise.all([applyDelay, lensTurnedOnTime]).then(async ([applyDelaySec, lensTurnedOnTime]) => {
                const onSessionSuspended = () => {
                    hasSuspended = true;
                    const viewTimeSec = (getTimeMs() - lensTurnedOnTime) / 1000;
                    maybeDispatchLensView({ lensId, applyDelaySec, viewTimeSec });
                };

                const onSessionResumed = () => {
                    const now = Promise.resolve(getTimeMs());
                    measureLensView(lensId, now, now, lensTurnedOffTime);
                };

                stateTarget.addEventListener("sessionSuspended", onSessionSuspended, { once: true });
                stateTarget.addEventListener("sessionResumed", onSessionResumed, { once: true });
                await lensTurnedOffTime.catch(() => {});
                stateTarget.removeEventListener("sessionSuspended", onSessionSuspended);
                stateTarget.removeEventListener("sessionResumed", onSessionResumed);
            });

            const [applyDelaySec, viewTimeSec] = await Promise.all([
                applyDelay,
                Promise.all([lensTurnedOnTime, lensTurnedOffTime]).then(([lensTurnedOnTime, lensTurnedOffTime]) => {
                    return (lensTurnedOffTime - lensTurnedOnTime) / 1000;
                }),
                // This could occur if lens B is somehow initiated before lens A is turned off. CameraKitSession's
                // internal state machine should prevent this. If it does happen, at least for now we'll miss the
                // lensView for lens A.
            ]).catch(() => [0, 0]);

            // If we've been suspended, we'll have already dispatched the metric. This could happen if the lens is
            // turned off while the tab is hidden.
            if (hasSuspended) return;
            maybeDispatchLensView({ lensId, applyDelaySec, viewTimeSec });
        };

        /**
         * Each time a "lensInitiated" event occurs, we will wait for subsequent events from which we can
         * gather the data required to record a CameraKitLensSwipe event.
         */
        stateTarget.addEventListener("lensInitiated", async ({ detail: lens }) => {
            // If a lens is initiated before the prior lens received its lensTurnedOff event, we'll abort the prior
            // lens's reporting and remove its event listeners. This isn't expected to happen; it's a safety measure to
            // prevent memory leaks.
            //
            // TODO: This logic will need to be updated when we implement multiple lenses applied at the same time (lens
            // layering)  we won't be able to remove listeners when a new lens is applied. This may be fine, we'll just
            // have to make sure the lensTurnedOff event is robust, else we'll get memory leakage.
            abortController.abort();
            abortController = new AbortController();

            const isSameLens = (maybeSameLens: Lens) => maybeSameLens.id === lens.id;
            const options: NextEventOptions<Lens> = { signal: abortController.signal, predicate: isSameLens };
            const lensLoadedTime = nextEvent(stateTarget, "lensLoaded", options).then(getTimeMs);
            const lensTurnedOnTime = nextEvent(stateTarget, "lensTurnedOn", options).then(getTimeMs);
            const lensTurnedOffTime = nextEvent(stateTarget, "lensTurnedOff", options).then(getTimeMs);

            measureLensView(lens.id, lensLoadedTime, lensTurnedOnTime, lensTurnedOffTime);
        });
    }
);
