import { ConfigResult, ConfigTargetingResponse, Namespace } from "@snapchat/configs-web";
import { wrapError } from "../../common/errors";
import { memoize } from "../../common/memoize";
import { isRecord, isString } from "../../common/typeguards";
import { Injectable } from "../../dependency-injection/Injectable";
import { createArrayBufferParsingHandler } from "../../handlers/arrayBufferParsingHandler";
import { defaultFetchHandlerFactory, FetchHandler } from "../../handlers/defaultFetchHandler";
import { HandlerChainBuilder } from "../../handlers/HandlerChainBuilder";
import { AssetDescriptor } from "../../lens-core-module";
import { CofHandler, cofHandlerFactory } from "../../handlers/cofHandler";
import { CameraKitConfiguration, configurationToken } from "../../configuration";
import { getLogger } from "../../logger";
import { AssetLoader, AssetResponse } from "./LensAssetsProvider";

interface AssetConfig {
    url: string;
    checksum?: string;
}

const logger = getLogger("deviceDependentAssetLoader");

const hasStringValue = (value: unknown): value is { stringValue: string } => {
    return isRecord(value) && isString(value.stringValue);
};

const isAssetConfig = (value: unknown): value is AssetConfig => {
    return isRecord(value) && isString(value.url) && (value.checksum === undefined || isString(value.checksum));
};

function createAssetIdToUrlMap(configResults: ConfigResult[]) {
    return configResults.reduce<Map<string, AssetConfig>>((configs, { configId, value }) => {
        // Asset configurations are all JSON-encoded in the `stringValue` property. If it doesn't exists, we can skip
        // this configuration here.
        if (!hasStringValue(value)) return configs;

        try {
            const config = JSON.parse(value.stringValue);

            // Asset configurations have a `url` and `checksum` property. Otherwise we're dealing with some other kind
            // of configuration, which we can skip.
            if (!isAssetConfig(config)) return configs;

            configs.set(configId, config);
            return configs;
        } catch (error) {
            logger.warn(
                `COF config with id=${configId} has "stringValue" that can't be parsed as JSON. If this is a ` +
                    `device-dependent asset configuration with malformed JSON, we may be unable to fetch the ` +
                    `corresponding asset.`
            );
        }

        return configs;
    }, new Map());
}

/**
 * @internal
 */
export const deviceDependentAssetLoaderFactory = Injectable(
    "deviceDependentAssetLoader",
    [defaultFetchHandlerFactory.token, cofHandlerFactory.token, configurationToken] as const,
    (fetchHandler: FetchHandler, cofHandler: CofHandler, config: CameraKitConfiguration): AssetLoader => {
        const assetHandler = new HandlerChainBuilder(fetchHandler).map(createArrayBufferParsingHandler()).handler;

        const cofConfigPromise = new Promise<ConfigTargetingResponse>(async (resolve, reject) => {
            const lensPerformance = await config.lensPerformance;
            // -1 is interpreted as "no cluster could be computed", so we'll omit in that case.
            const lensClusterOrig4 = lensPerformance?.cluster === -1 ? undefined : lensPerformance?.cluster;
            cofHandler([{ namespaces: [Namespace.LENS_CORE], lensClusterOrig4 }])
                .then(resolve)
                .catch(reject);
        });

        const memoizedCreateAssetIdToUrlMap = memoize(createAssetIdToUrlMap);

        return async function deviceDependentAssetLoader({ assetId }: AssetDescriptor): Promise<AssetResponse> {
            const cofConfig = await cofConfigPromise.catch((error) => {
                throw wrapError(new Error("Failed to fetch lens asset configuration from COF."), error);
            });
            const assetConfig = memoizedCreateAssetIdToUrlMap(cofConfig.configResults).get(assetId);
            if (!assetConfig) {
                throw new Error(`Cannot load lens asset ${assetId}. No config found in COF for this asset.`);
            }
            const [buffer, response] = await assetHandler(assetConfig.url);
            if (!response.ok) throw response;
            return { data: buffer, checksum: assetConfig.checksum };
        };
    }
);
