export type NamedError<Name extends string> = Error & { name: Name };

/**
 * Creates error factory that ensures Error.prototype.name field value.
 *
 * @internal
 *
 * @param name Error name.
 * @returns Error factory function.
 */
export function namedError<Name extends string, ActualError extends NamedError<Name>>(name: Name) {
    return (error: Error): ActualError => {
        error.name = name;
        // Safety: we set name above and therefore sure the type of error is correct
        return error as ActualError;
    };
}

/**
 * Wrap an inner error with an outer error to provide additional context to an error as it bubbles up the call stack.
 *
 * @param outerError This error will wrap the inner error, allowing the caller to provide additional context e.g. when
 * re-throwing a caught error.
 * @param innerErrorLike This error's stacktrace will be appended to the outer error stacktrace.
 * @returns The outer error with the inner error's stacktrace appended to its own.
 */
export function wrapError<T extends Error>(outerError: T, innerErrorLike: unknown): T {
    // NOTE: The recommended way to nest errors in JS is Error.prototype.cause now:
    // https://developer.mozilla.org/en-US/docs/Web/JavaScript/Reference/Global_Objects/Error/cause
    // That is supported by Chrome, Firefox, Sentry and others.
    // While FireFox DevTools Console prints inner errors in "cause" field properly,
    // Chrome DevTools Console on the other hand still does not do that:
    // https://bugs.chromium.org/p/chromium/issues/detail?id=1211260
    // In order to improve debugging efficiency in Chrome, we append the inner error stack
    // to the outer one until the Chrome issue is addressed.
    const innerError = ensureError(innerErrorLike);
    // We'll try to replicate Java's stacktrace format for chained exceptions.
    const innerErrorStack = innerError.stack ?? "";
    const innerErrorMessage = innerErrorStack.includes(innerError.message)
        ? innerError.stack
        : `${innerError.message}\n${innerError.stack}`;

    outerError.stack = `${outerError.stack}\nCaused by: ${innerErrorMessage}`;
    outerError.cause = innerError;
    return outerError;
}

/**
 * If given a value of type Error, return it  otherwise wrap the value in an Error.
 */
export function ensureError(error: unknown): Error {
    if (error instanceof Error) return error;

    try {
        return new Error(`Non-Error type exception thrown. Serialized error value: ${JSON.stringify(error)}`);
    } catch (_) {
        return new Error("Non-Error type exception thrown. Original error value could not be serialized.");
    }
}
