/* eslint-disable @typescript-eslint/member-ordering */
import { Lens, LensRepository, isLens, lensRepositoryFactory } from "../lens";
import { AddLensInput, CanvasType, LensCoreModule } from "../lens-core-module";
import { Injectable } from "../dependency-injection/Injectable";
import { LensAssetsProvider, lensAssetsProviderFactory } from "../lens/assets/LensAssetsProvider";
import { lensCoreFactory } from "../lens-core-module/loader/lensCoreFactory";
import { defaultFetchHandlerFactory, FetchHandler } from "../handlers/defaultFetchHandler";
import { Handler, HandlerChainBuilder } from "../handlers/HandlerChainBuilder";
import { createArrayBufferParsingHandler } from "../handlers/arrayBufferParsingHandler";
import { createRequestStateEmittingHandler } from "../handlers/requestStateEmittingHandler";
import { LensLaunchParams, isLaunchParamsValid, createLaunchData } from "../lens/LensLaunchParams";
import { onPageHidden, onPageVisible } from "../common/pageVisibility";
import { guard, validate } from "../common/validate";
import { TypedCustomEvent } from "../events/TypedCustomEvent";
import { TypedEventTarget } from "../events/TypedEventTarget";
import { CameraKitDeviceInfo, CameraKitSource } from "../media-sources/CameraKitSource";
import { wrapError } from "../common/errors";
import { createMediaStreamSource } from "../media-sources/MediaStreamSource";
import { createVideoSource } from "../media-sources/VideoSource";
import { FrameProcessingMetrics } from "../metrics/FrameProcessingMetrics";
import { LensDownloadDimensions } from "../metrics/reporters/reportLensAndAssetDownload";
import { LensContext, lensContextFactory } from "./LensContext";
import { SessionStateEventTarget, sessionStateEventTargetFactory, SessionLensEvents } from "./sessionStateEventTarget";

type LensHandler = Handler<[RequestInfo, LensDownloadDimensions], [ArrayBuffer, Response], undefined>;

type RenderTarget = "live" | "capture" | "all";

function isOptionalRenderTarget(value: unknown): value is RenderTarget | undefined {
    return value === "live" || value === "capture" || value === "all" || value === undefined;
}

export interface CameraKitSessionMetrics {
    frames: FrameProcessingMetrics["metrics"];
}

export type CameraKitSessionEvents = TypedCustomEvent<"error", Error>;

/**
 * A CameraKitSession represents a single rendering pipeline connecting an input source to output `<canvas>` elements.
 * When a Lens is applied to the session, it provides assets and rendering logic to transform the input media.
 */
export class CameraKitSession {
    /**
     * CameraKitSession renders video output to a `<canvas>` element. In fact, each session contains two canvas outputs
     * corresponding to the RenderTargets used by Lens creators.
     *
     * The "live" output renders content suitable for the Lens user (e.g. it may contain additional UI elements
     * applicable only to the person applying the lens). The "capture" output renders content suitable for sharing with
     * other users (e.g. sent to the other members of a video call).
     *
     * For many lenses, these outputs are identical  many lens use-cases will only render the "live" output.
     */
    readonly output: {
        live: HTMLCanvasElement;
        capture: HTMLCanvasElement;
    };

    playing: false | RenderTarget;

    private readonly frameProcessingMetrics: FrameProcessingMetrics;
    private readonly removePageVisibilityHandlers: () => void;
    private source?: CameraKitSource;

    constructor(
        private readonly lensCore: LensCoreModule,

        /**
         * Events occuring during the lifetime of the session are emitted here.
         *
         * These events include:
         *
         * - `error`: Emitted whenever the core Lens rendering engine encounters an error during image processing.
         */
        public readonly events: TypedEventTarget<CameraKitSessionEvents>,
        private readonly fetchLens: LensHandler,
        private readonly lensAssetsProvider: LensAssetsProvider,
        private readonly lensRepository: LensRepository,
        private readonly lensContext: LensContext,
        private readonly stateTarget: SessionStateEventTarget
    ) {
        const outputs = this.lensCore.getOutputCanvases();
        this.output = {
            live: outputs[this.lensCore.CanvasType.Preview.value],
            capture: outputs[this.lensCore.CanvasType.Capture.value],
        };
        this.playing = false;
        this.frameProcessingMetrics = new FrameProcessingMetrics(this.lensCore);
        this.stateTarget.dispatchEvent(new TypedCustomEvent("sessionCreated", {}));

        const removeOnHidden = onPageHidden(() => {
            this.stateTarget.dispatchEvent(new TypedCustomEvent("sessionSuspended", {}));
        });
        const removeOnVisible = onPageVisible(() => {
            this.stateTarget.dispatchEvent(new TypedCustomEvent("sessionResumed", {}));
        });
        this.removePageVisibilityHandlers = () => {
            removeOnHidden();
            removeOnVisible();
        };
    }

    get metrics(): CameraKitSessionMetrics {
        return { frames: this.frameProcessingMetrics.metrics };
    }
    /**
     * Apply a Lens to this session.
     *
     * This method will download (and cache) the Lens executable, and then use that Lens for rendering. If the session
     * is currently playing, this will immediately update the rendered output. Otherwise, the new Lens will be used
     * when session playback in resumed.
     *
     * Calling `applyLens` replaces any prior lens  only one Lens is allowed at a time (per session).
     *
     * @param lens The Lens to apply to this session.
     * @returns Promise resolves when the lens has been successfully applied (i.e. any frames rendered after this
     * point will use the new lens); rejects if:
     *   1. Lens content object is invalid.
     *   1. The prior lens fails to be removed.
     *   1. Lens download fails.
     *   1. Lens application fails.
     */
    @validate
    async applyLens(@guard(isLens) lens: Lens, launchParams?: LensLaunchParams): Promise<void> {
        // Validation guarantees JSON.stringify and Protobuf encode will succeed, otherwise this function throws
        const binaryLaunchData =
            launchParams && isLaunchParamsValid(launchParams) ? createLaunchData(launchParams) : null;

        await this.removeLens();

        return new Promise(async (resolve, reject) => {
            try {
                const dispatch = (eventType: SessionLensEvents["type"]) => {
                    this.stateTarget.dispatchEvent(new TypedCustomEvent(eventType, lens));

                    // Performance varies widely between lenses (and between lens applied and no lens applied), so our
                    // frame processing metrics make sense only if they cover just a single lens.
                    if (eventType === "lensTurnedOff" || eventType === "lensTurnedOn") {
                        // Schedule the reset for the next tick of the event loop, this allows any event listeners to
                        // run to completion first  they might want to read these metrics before they're reset.
                        setTimeout(() => this.frameProcessingMetrics.reset());
                    }
                };

                dispatch("lensApplyStart");

                const cachedLens = this.lensRepository.getCachedLens(lens.id);
                if (!cachedLens) {
                    return reject(
                        new Error(
                            `Cannot apply lens ${lens.id}. It has not been loaded by the Lens repository. Use ` +
                                `CameraKit.lenses.repository.loadLens (or loadLensGroups) to load lens metadata ` +
                                `before calling CameraKitSession.applyLens.`
                        )
                    );
                }
                const { id, content } = cachedLens;
                if (!content) {
                    return reject(
                        new Error(
                            `Cannot apply lens ${lens.id}. Metadata retrieved for this lens does not include the ` +
                                `lens content URL.`
                        )
                    );
                }

                this.updateLensContext({ activeLens: cachedLens });

                // Load required lens assets and the lens itself in parallel. We count both toward lens download time.
                const [_, [lensDataBuffer]] = await Promise.all([
                    // If any required asset from the manifest fails, we stop trying to apply the lens.
                    this.lensAssetsProvider.preloadManifestData(content!.assetManifest),
                    this.fetchLens([new Request(content.lnsUrlBolt), { requestType: "lens_content", lensId: id }]),
                    // We want to dispatch on both success and failure, so we use finally.
                ]).finally(() => dispatch("lensDownloadComplete"));

                const lensInput: AddLensInput = {
                    lensId: id,
                    lensDataBuffer,
                    lensChecksum: content!.lnsSha256,
                    onSuccess: resolve,
                    onFailure: (error: Error) => {
                        this.removeLens();
                        reject(error);
                        if (/Attempting to add lens from \S*, but checksum validation failed./.test(error.message)) {
                            dispatch("lensContentValidationFailure");
                        }
                    },
                    onTurnOn: () => dispatch("lensTurnedOn"),
                    onTurnOff: () => dispatch("lensTurnedOff"),
                    onResourcesLoaded: () => dispatch("lensLoaded"),
                    onFirstFrameProcessed: () => dispatch("firstFrameProcessed"),
                };

                if (binaryLaunchData) {
                    lensInput.launchData = binaryLaunchData;
                }

                dispatch("lensInitiated");
                this.lensCore.addLens(lensInput);
            } catch (error) {
                await this.removeLens();
                reject(error);
            }
        });
    }

    /**
     * Remove a Lens from this session.
     *
     * When a Lens is removed, rendering continues if the session is playing. It will just render the session input
     * directly to the outputs without any image processing.
     *
     * @returns Promise resolves when the lens has been successfully removed (i.e. any frames rendered after this point
     * will use no lens); rejects if removal fails.
     */
    async removeLens(): Promise<void> {
        await new Promise((resolve, reject) =>
            this.lensCore.clearAllLenses({
                onSuccess: resolve,
                onFailure: reject,
            })
        );

        this.updateLensContext({ activeLens: undefined });
    }

    /**
     * Start/resume session playback  LensCore will begin rendering frames to the output.
     *
     * If no source has been set for the session, calling `play()` will update the playing state, but no actual image
     * processing will occur until `setSource()` is called.
     *
     * TODO: The above is currently not true. Right now `playCanvas` is stateful  that is, it only works if
     * `useMediaElement` has previously succeeded (if this is called *before* `setSource` it will never resolve). We
     * should remove this constraint so callers don't have to understand the underlying LensCore state machine.
     *
     * TODO: There seems to be a bug in the WASM whereby passing Live here does not start rendering any output, and
     * passing in Capture starts rendering both Capture *and* Live outputs.
     *
     * @param target Specify the RenderTarget to begin playback. May be either `'live'`, `'capture'`, or `'all'`.
     * Default is `'live'`.
     * @returns Promise resolves when playback has begun (immediately after the first frame has rendered).
     */
    @validate
    async play(@guard(isOptionalRenderTarget) target: RenderTarget = "live"): Promise<void> {
        if (this.playing) return;
        this.playing = target;

        const type = this.renderTargetToCanvasType(target);
        return new Promise((resolve, reject) => {
            this.lensCore.playCanvas({
                type,
                onReady: resolve,
                onFailure: (error: Error) => {
                    this.playing = false;
                    reject(error);
                },
            });
        });
    }

    /**
     * Pause session playback  LensCore will stop rendering frames to the output.
     *
     * @param target Specify the RenderTarget to pause playback. May be either `'live'`, `'capture'`, or `'all'`.
     * Default is `'all'`.
     * @returns Promise resolves when playback has stopped.
     */
    @validate
    async pause(@guard(isOptionalRenderTarget) target: RenderTarget = "all"): Promise<void> {
        if (!this.playing) return;
        const priorPlayingState = this.playing;
        this.playing = false;

        const type = this.renderTargetToCanvasType(target);
        return new Promise((resolve, reject) => {
            this.lensCore.pauseCanvas({
                type,
                onSuccess: resolve,
                onFailure: (error: Error) => {
                    this.playing = priorPlayingState;
                    reject(error);
                },
            });
        });
    }

    /**
     * Set the media source for this session.
     *
     * Sessions may only have one source at a time - if `setSource` is called multiple times, subsequent calls replace
     * the prior source. Setting the source does not trigger rendering (thats done by `session.play()`). If the session
     * is already playing, setting the source will immediately begin rendering the new source.
     *
     * @param source Source media (e.g. a webcam stream, video, or some other source of image data), which CameraKit
     * will process and onto which CameraKit can apply Lenses.
     * @returns Promise is resolved when the source has successfully be set. If the session was already in the playing
     * state, the Promise resolves when the first frame from the new source has been rendered. The resolved value is
     * the {@link CameraKitSource} object attached to the session.
     */
    async setSource(source: CameraKitSource): Promise<CameraKitSource>;
    async setSource(
        source: MediaStream | HTMLVideoElement,
        options?: Partial<CameraKitDeviceInfo>
    ): Promise<CameraKitSource>;
    @validate
    async setSource(
        source: CameraKitSource | MediaStream | HTMLVideoElement,
        options: Partial<CameraKitDeviceInfo> = {}
    ): Promise<CameraKitSource> {
        await this.safelyDetachSource();

        // For convenience, we allow callers to pass in native objects (e.g. MediaStream) as well as CameraKitSource.
        // Native objects are wrapped in corresponding CameraKitSource classes with default options.
        const cameraKitSource =
            source instanceof MediaStream
                ? createMediaStreamSource(source, options)
                : source instanceof HTMLVideoElement
                ? createVideoSource(source, options)
                : source;

        const priorPlayingState = this.playing;
        this.playing = false;

        // The source will provide its data to LensCore, and use other LensCore APIs (e.g. setRenderSize,
        // setInputTransform) to render the source correctly.
        await cameraKitSource.attach(this.lensCore, (error) => {
            this.events.dispatchEvent(new TypedCustomEvent("error", error));
        });

        // If attachment is successful, we'll update our source so that we can detach it later.
        this.source = cameraKitSource;

        // Finally we'll resume playback, if appropriate.
        if (priorPlayingState) {
            await this.play(priorPlayingState);
        }

        return cameraKitSource;
    }

    /**
     * Set an FPS limit.
     *
     * This may be useful to reduce CPU/GPU resource usage by CameraKit if, for example, the input
     * media source has a low FPS  CameraKit would then not try to render more frequently than the source produces
     * new frames.
     *
     * This may also be useful to gracefully degrade performance in situations where lowering FPS is preferable over
     * alternatives.
     *
     * @param fpsLimit A maximum FPS, rendering will not exceed this limit
     * @returns Promise is resolved when the limit is successfully set.
     */
    async setFPSLimit(fpsLimit: number): Promise<void> {
        // LensCore uses 0 to remove the limit.
        const fps = fpsLimit < Number.POSITIVE_INFINITY ? fpsLimit : 0;
        return new Promise((onSuccess, onFailure) => {
            this.lensCore.setFPSLimit({ fps, onSuccess, onFailure });
        });
    }

    /**
     * Destroy the session.
     *
     * The session will become inoperable. Frame processing stops, and any session-scoped resources are freed.
     */
    async destroy(): Promise<void> {
        await this.removeLens();
        await this.safelyDetachSource();
        await new Promise<void>((resolve, reject) => {
            this.lensCore.teardown({ onSuccess: resolve, onFailure: reject });
        });
        this.removePageVisibilityHandlers();
        this.stateTarget.dispatchEvent(new TypedCustomEvent("sessionDestroyed", {}));
    }

    private renderTargetToCanvasType(t: RenderTarget): CanvasType {
        return t === "live"
            ? this.lensCore.CanvasType.Preview
            : t === "capture"
            ? this.lensCore.CanvasType.Capture
            : this.lensCore.CanvasType.All;
    }

    private async safelyDetachSource(): Promise<void> {
        if (this.source) {
            try {
                await this.source.detach();
                // If there's a failure to detach, we will report the error and proceed. Failure to detach may lead to a
                // memory leak, but it shouldn't prevent us from switching to the new source.
            } catch (error) {
                this.events.dispatchEvent(
                    new TypedCustomEvent(
                        "error",
                        wrapError(new Error(`Detaching prior source of type ${typeof this.source} failed.`), error)
                    )
                );
            }
        }
    }

    private updateLensContext(partialContext: Partial<LensContext>) {
        Object.assign(this.lensContext, partialContext);
    }
}

/**
 * @internal
 */
export const cameraKitSessionFactory = Injectable(
    "CameraKitSession",
    [
        lensCoreFactory.token,
        "eventTarget",
        lensAssetsProviderFactory.token,
        defaultFetchHandlerFactory.token,
        lensRepositoryFactory.token,
        lensContextFactory.token,
        sessionStateEventTargetFactory.token,
    ] as const,
    (
        lensCore: LensCoreModule,
        eventTarget: TypedEventTarget<CameraKitSessionEvents>,
        lensAssetsProvider: LensAssetsProvider,
        fetchHandler: FetchHandler,
        lensRepository: LensRepository,
        lensContext: LensContext,
        stateTarget: SessionStateEventTarget
    ) =>
        new CameraKitSession(
            lensCore,
            eventTarget,
            new HandlerChainBuilder(fetchHandler)
                .map(createRequestStateEmittingHandler<LensDownloadDimensions>())
                .map(createArrayBufferParsingHandler()).handler,
            lensAssetsProvider,
            lensRepository,
            lensContext,
            stateTarget
        )
);
