import { assertUnreachable } from "../../common/assertions";
import { wrapError } from "../../common/errors";
import { Injectable } from "../../dependency-injection/Injectable";
import {
    AssetDescriptor,
    AssetType,
    lensCoreFactory,
    LensCoreModule,
    ProvideRemoteAssetsResponseInput,
} from "../../lens-core-module";
import {
    LensAssetManifestItem,
    LensAssetManifestItem_RequestTiming,
    LensAssetManifestItem_Type,
} from "../../generated-proto/pb_schema/camera_kit/v3/lens";
import { Lens, toPublicLens } from "../Lens";
import {
    dispatchRequestCompleted,
    dispatchRequestErrored,
    dispatchRequestStarted,
} from "../../handlers/requestStateEmittingHandler";
import { AssetDownloadDimensions } from "../../metrics/reporters/reportLensAndAssetDownload";
import { LensContext, lensContextFactory } from "../../session/LensContext";
import { getLogger } from "../../logger";
import { SessionStateEventTarget, sessionStateEventTargetFactory } from "../../session/sessionStateEventTarget";
import { TypedCustomEvent } from "../../events/TypedCustomEvent";
import { deviceDependentAssetLoaderFactory } from "./deviceDependentAssetLoader";
import { remoteMediaAssetLoaderFactory } from "./remoteMediaAssetLoaderFactory";
import { staticAssetLoaderFactory } from "./staticAssetLoader";

const logger = getLogger("LensAssetProvider");

// Request timings that are allowed to be pre-loaded
const assetRequestTimingsToPreload = new Set([
    LensAssetManifestItem_RequestTiming.PRELOAD_UNSET,
    LensAssetManifestItem_RequestTiming.REQUIRED,
]);

export type AssetResponse =
    | ArrayBuffer
    | {
          data: ArrayBuffer;
          checksum?: string;
      };

/**
 * An AssetLoader is used to retrieve assets. A separate loader may be defined to retrieve different asset types.
 */
export type AssetLoader = (asset: AssetDescriptor, lens: Lens) => Promise<AssetResponse> | AssetResponse;

/**
 * Registers a remote asset provider function with a given instance of LensCore, and uses a provided mapping of asset
 * types to loading functions to acquire remote asset data and pass it to LensCore.
 *
 * *Note:* LensCoreModule.initialize must be called on the desired LensCoreModule instance **prior** to passing it
 * to the LensAssetsProvider constructor. If this class is instantiated with a LensCoreModule that has not been
 * initialized, the registry of the asset provider function will fail silently and no remote assets will be loaded.
 */
export class LensAssetsProvider {
    constructor(
        private readonly lensCore: LensCoreModule,
        private readonly assetLoaders: Map<AssetType, [keyof LensCoreModule["AssetType"], AssetLoader]>,
        private readonly lensContext: LensContext,
        private readonly stateTarget: SessionStateEventTarget
    ) {
        lensCore.setRemoteAssetsProvider((assetDescriptor) => {
            // Catch errors and no-op  if an error occurs, LensCore handles things in different ways depending on the
            // active lens. It might: 1) retry 2) gracefully degrade lens behavior 3) lens JS might throw, which will
            // be passed to the exceptionHandler we register with LensCore.
            this.provideRemoteAsset(assetDescriptor).catch((error) => logger.warn(error));
        });
    }

    /**
     * Returns asset data if it is eligible to be preloaded.
     *
     * @param assets An array of asset manifest items to preload.
     * @returns Promise rejects if any required assets could not be loaded  if this occurs, it's very likely the Lens
     * with this manifest will not function.
     */
    async preloadManifestData(assets: LensAssetManifestItem[]): Promise<void> {
        await Promise.all(
            assets
                .filter((asset) => {
                    return assetRequestTimingsToPreload.has(asset.requestTiming);
                })
                .map(({ id, type }) => {
                    // When preloading, we *do* want load failures to reject Promise.all (assets listed in the manifest
                    // are known to be hard requirements of the lens).
                    return this.provideRemoteAsset({
                        assetId: id,
                        assetType: this.convertToLensCoreAssetType(type),
                    });
                })
        );
    }

    private convertToLensCoreAssetType(type: LensAssetManifestItem_Type): AssetType {
        switch (type) {
            case LensAssetManifestItem_Type.ASSET:
                return this.lensCore.AssetType.Static;
            case LensAssetManifestItem_Type.DEVICE_DEPENDENT_ASSET_UNSET:
            case LensAssetManifestItem_Type.UNRECOGNIZED:
                return this.lensCore.AssetType.DeviceDependent;
            default:
                return assertUnreachable(type);
        }
    }

    /**
     * Depending on the requested asset's type, calls the correct asset loader to fetch the asset's data.
     */
    private async getAsset(assetDescriptor: AssetDescriptor): Promise<ProvideRemoteAssetsResponseInput> {
        const { assetId, assetType } = assetDescriptor;
        const [assetTypeName, assetLoader] = this.assetLoaders.get(assetType) ?? [];
        const dimensions: AssetDownloadDimensions = {
            requestType: "asset",
            assetId: assetId,
            assetType: assetTypeName ?? "unknown",
            lensId: this.lensContext.activeLens?.id ?? "unknown",
        };

        const { requestId } = dispatchRequestStarted({ dimensions });

        try {
            if (!assetLoader) {
                throw new Error(`Cannot get asset ${assetId}. Asset type "${assetType.value}" is not supported.`);
            }

            if (!this.lensContext.activeLens) {
                throw new Error(
                    `Cannot get asset ${assetId}. Request must be made while a lens is applied, but no lens is ` +
                        `currently applied.`
                );
            }

            const assetResponse = await assetLoader(assetDescriptor, toPublicLens(this.lensContext.activeLens));
            const assetBuffer = assetResponse instanceof ArrayBuffer ? assetResponse : assetResponse.data;
            const assetChecksum = assetResponse instanceof ArrayBuffer ? undefined : assetResponse.checksum;

            const sizeByte = assetBuffer.byteLength;
            if (sizeByte === 0) {
                // We want to replace empty strings with "anonymous" (i.e. use of logical OR instead of the nullish
                // coalescing operator is intentional).
                const assetLoaderName = assetLoader.name || "anonymous";
                throw new Error(
                    `Got empty response for asset ${assetId} of type ${assetTypeName ?? "unknown"} from ` +
                        `assetLoader ${assetLoaderName}.`
                );
            }

            dispatchRequestCompleted({ requestId, dimensions, status: 200, sizeByte });

            return {
                assetId,
                assetBuffer,
                assetType,
                assetChecksum,
                onFailure: (error) => {
                    if (/Checksum validation failed/.test(error.message)) {
                        this.stateTarget.dispatchEvent(new TypedCustomEvent("assetValidationFailure", assetDescriptor));
                    }
                },
            };
        } catch (error) {
            const wrappedError = wrapError(new Error(`Failed to load lens asset ${assetId}.`), error);
            dispatchRequestErrored({ requestId, dimensions, error: wrappedError });
            throw wrappedError;
        }
    }

    /**
     * Fetch an asset and provide it to LensCore. If fetching the asset fails we give LensCore an empty response (which
     * it may handle in a variety of ways, e.g. retry, gracefully degrade lens behavior, throw error) and then reject.
     */
    private async provideRemoteAsset(assetDescriptor: AssetDescriptor): Promise<void> {
        try {
            const asset = await this.getAsset(assetDescriptor);
            this.lensCore.provideRemoteAssetsResponse(asset);
        } catch (error) {
            this.lensCore.provideRemoteAssetsResponse({
                assetId: assetDescriptor.assetId,
                assetType: assetDescriptor.assetType,
            });
            throw error;
        }
    }
}

/**
 * @internal
 */
export const lensAssetsProviderFactory = Injectable(
    "lensAssetsProvider",
    [
        lensCoreFactory.token,
        lensContextFactory.token,
        deviceDependentAssetLoaderFactory.token,
        remoteMediaAssetLoaderFactory.token,
        staticAssetLoaderFactory.token,
        sessionStateEventTargetFactory.token,
    ] as const,
    (
        lensCore: LensCoreModule,
        lensContext: LensContext,
        deviceDependentAssetLoader: AssetLoader,
        remoteMediaAssetLoader: AssetLoader,
        staticAssetLoaderFactory: AssetLoader,
        stateTarget: SessionStateEventTarget
    ) =>
        new LensAssetsProvider(
            lensCore,
            new Map([
                [lensCore.AssetType.DeviceDependent, ["DeviceDependent", deviceDependentAssetLoader]],
                [lensCore.AssetType.RemoteMediaByUrl, ["RemoteMediaByUrl", remoteMediaAssetLoader]],
                // URL type is deprecated and was introduced before RemoteMediaByURL
                // however, there are some lenses still using it so we need to support it
                [lensCore.AssetType.URL, ["URL", remoteMediaAssetLoader]],
                [lensCore.AssetType.Static, ["Static", staticAssetLoaderFactory]],
            ]),
            lensContext,
            stateTarget
        )
);
