import { lensRepositoryFactory, LensRepository } from "./lens/LensRepository";
import { CONTAINER, Container } from "./dependency-injection/Container";
import { Injectable } from "./dependency-injection/Injectable";
import { LensCoreModule } from "./lens-core-module/generated-types";
import { CameraKitSession, cameraKitSessionFactory, CameraKitSessionEvents } from "./session/CameraKitSession";
import { lensAssetsProviderFactory } from "./lens/assets/LensAssetsProvider";
import { lensCoreFactory } from "./lens-core-module/loader/lensCoreFactory";
import { configurationToken } from "./configuration";
import { RootServices } from "./dependency-injection/RootServices";
import { TypedEventTarget } from "./events/TypedEventTarget";
import { TypedCustomEvent } from "./events/TypedCustomEvent";
import { UriHandlers, uriHandlersFactory } from "./extensions/UriHandlers";
import { registerUriHandlers } from "./extensions/UriHandlers";
import { LensContext, lensContextFactory } from "./session/LensContext";
import { sessionStateEventTargetFactory } from "./session/sessionStateEventTarget";
import { MetricsEventTarget, metricsEventTargetFactory } from "./metrics/metricsEventTarget";
import { sessionScopedMetricsReporters } from "./metrics/reporters/reporters";
import { reportUserSession } from "./metrics/reporters/reportUserSession";

/**
 * CameraKit's public components (namespaced into feature areas).
 */
interface CameraKitComponents {
    lenses: {
        repository: {
            loadLens: LensRepository["loadLens"];
            loadLensGroups: LensRepository["loadLensGroups"];
        };
    };
    metrics: MetricsEventTarget;
}

interface CreateSessionsOptions {
    /** Optionally provide an existing canvas element, on which the Live RenderTarget will be rendered.
     * If this is not provided, CameraKit will create a new canvas element which can be added to the DOM.*/
    liveRenderTarget?: HTMLCanvasElement;
    /** Optionally provide boolean value, to determine whether to use web worker or not.
     * Default is true, which will not allow tab backgroundVisibility change. */
    renderWhileTabHidden?: boolean;
}

/**
 * Entry point to all CameraKit components, used to create {@link CameraKitSession}
 * instances (which implement the main bulk of the CameraKit API).
 *
 * Also provides access to various other CameraKit components:
 * - A Lens repository instance
 * - *more to come?*
 */
export class CameraKit {
    readonly lenses: CameraKitComponents["lenses"];
    readonly metrics: CameraKitComponents["metrics"];

    constructor(
        { lenses, metrics }: CameraKitComponents,
        private readonly container: Container<RootServices>,
        private readonly lensCore: LensCoreModule,
        private readonly uriHandlers: UriHandlers,
        private readonly lensContext: LensContext
    ) {
        this.lenses = lenses;
        this.metrics = metrics;
    }

    /**
     * Create a CameraKitSession.
     *
     * This will initialize the core Lens rendering engine and return a CameraKitSession instance, which can be used to
     * add a media source, apply lenses, and access outputs.
     *
     * @param options configuration object when creating session
     * @returns Promise<CameraKitSession>
     */
    readonly createSession = async ({
        liveRenderTarget,
        renderWhileTabHidden,
    }: CreateSessionsOptions = {}): Promise<CameraKitSession> => {
        // We'll proxy errors from LensCore (which will call an exceptionHandler each time a rendering error occurs) to
        // an EventTarget  this target will be used by CameraKitSession to expose error events to consumers.
        const eventTarget = new TypedEventTarget<CameraKitSessionEvents>();
        const exceptionHandler = (error: Error) => {
            eventTarget.dispatchEvent(new TypedCustomEvent("error", error));
        };

        /**
         * If/when we add support for multiple concurrent sessions, we'll need to create a copy of the LensCore WASM
         * module. If we move managing web workers into JS, spawing a new worker thread with its own copy of LensCore
         * probably becomes a lot more straightforward.
         *
         * Currently chromium has a bug preventing rendering while tab is hidden when LensCore is in worker mode.
         * In order to process tab while it is hidden, the current stopgap is to pass in renderWhileTabHidden as true,
         * which will initiate session in non worker mode, and set the RenderLoopMode to `SetTimeout`.
         */
        await new Promise<void>((onSuccess, onFailure) => {
            this.lensCore.initialize({
                canvas: liveRenderTarget,
                shouldUseWorker: !renderWhileTabHidden && this.container.get(configurationToken).shouldUseWorker,
                exceptionHandler,
                onSuccess,
                onFailure,
            });
        });

        await new Promise<void>((onSuccess, onFailure) => {
            this.lensCore.setRenderLoopMode({
                mode: renderWhileTabHidden
                    ? this.lensCore.RenderLoopMode.SetTimeout
                    : this.lensCore.RenderLoopMode.RequestAnimationFrame,
                onSuccess,
                onFailure,
            });
        });
        registerUriHandlers(this.lensCore, this.uriHandlers, this.lensContext);

        // Each session gets its own DI Container  some Services provided by this Container may be shared with the
        // root CameraKit Container, but others may be scoped to the session by passing their token to `copy()`.
        const sessionContainer = this.container
            // Right now this is a no-op. If/when we add support for multiple concurrent sessions, we may end up
            // scoping LensCore to the session.
            .copy()
            .provides(Injectable("eventTarget", () => eventTarget))
            .provides(sessionStateEventTargetFactory)

            // The `reportUserSession` metrics reporter has to be added *here* so that it can start listening for
            // CameraKitSession state events (i.e. the "sessionCreated" event) before the session is created.
            .run(reportUserSession)

            // LensAssetsProvider can only be instantiated *after* LensCore::initialize() is called  otherwise the
            // call to LensCore::setRemoteAssetsProvider() will not work and no remote assets will be loaded. So we
            // provide it here, rather than in the RootContainer.
            .provides(lensAssetsProviderFactory)
            .provides(cameraKitSessionFactory)

            // We'll run a PartialContainer containing reporters for session-scoped metrics. Running this container
            // allows each metric reporter to initialize itself (e.g. by adding event listeners to detect when certain
            // actions occur).
            .run(sessionScopedMetricsReporters);

        return sessionContainer.get(cameraKitSessionFactory.token);
    };
}

/**
 * @internal
 */
export const cameraKitFactory = Injectable(
    "CameraKit",
    [
        CONTAINER,
        lensCoreFactory.token,
        uriHandlersFactory.token,
        lensContextFactory.token,
        metricsEventTargetFactory.token,
    ] as const,
    (
        container: Container<RootServices>,
        lensCore: LensCoreModule,
        uriHandlers: UriHandlers,
        lensContext: LensContext,
        metrics: MetricsEventTarget
    ) =>
        new CameraKit(
            {
                lenses: {
                    repository: container.get(lensRepositoryFactory.token),
                },
                metrics,
            },
            container,
            lensCore,
            uriHandlers,
            lensContext
        )
);
