/* eslint-disable */
import { util, configure, Writer, Reader } from "protobufjs/minimal";
import * as Long from "long";
import { Geocircle } from "../lenses/geocircle";

export const protobufPackage = "snapchat.lenses";

export interface Lures {
  locations: Geocircle[];
  /** Optional: lure which was activated (from map) */
  activeLure: Geocircle | undefined;
  /** Optional: lure which is closest to user */
  closestLure: Geocircle | undefined;
}

function createBaseLures(): Lures {
  return { locations: [], activeLure: undefined, closestLure: undefined };
}

export const Lures = {
  encode(message: Lures, writer: Writer = Writer.create()): Writer {
    for (const v of message.locations) {
      Geocircle.encode(v!, writer.uint32(10).fork()).ldelim();
    }
    if (message.activeLure !== undefined) {
      Geocircle.encode(message.activeLure, writer.uint32(18).fork()).ldelim();
    }
    if (message.closestLure !== undefined) {
      Geocircle.encode(message.closestLure, writer.uint32(26).fork()).ldelim();
    }
    return writer;
  },

  decode(input: Reader | Uint8Array, length?: number): Lures {
    const reader = input instanceof Reader ? input : new Reader(input);
    let end = length === undefined ? reader.len : reader.pos + length;
    const message = createBaseLures();
    while (reader.pos < end) {
      const tag = reader.uint32();
      switch (tag >>> 3) {
        case 1:
          message.locations.push(Geocircle.decode(reader, reader.uint32()));
          break;
        case 2:
          message.activeLure = Geocircle.decode(reader, reader.uint32());
          break;
        case 3:
          message.closestLure = Geocircle.decode(reader, reader.uint32());
          break;
        default:
          reader.skipType(tag & 7);
          break;
      }
    }
    return message;
  },

  fromJSON(object: any): Lures {
    return {
      locations: Array.isArray(object?.locations) ? object.locations.map((e: any) => Geocircle.fromJSON(e)) : [],
      activeLure: isSet(object.activeLure) ? Geocircle.fromJSON(object.activeLure) : undefined,
      closestLure: isSet(object.closestLure) ? Geocircle.fromJSON(object.closestLure) : undefined,
    };
  },

  toJSON(message: Lures): unknown {
    const obj: any = {};
    if (message.locations) {
      obj.locations = message.locations.map((e) => (e ? Geocircle.toJSON(e) : undefined));
    } else {
      obj.locations = [];
    }
    message.activeLure !== undefined &&
      (obj.activeLure = message.activeLure ? Geocircle.toJSON(message.activeLure) : undefined);
    message.closestLure !== undefined &&
      (obj.closestLure = message.closestLure ? Geocircle.toJSON(message.closestLure) : undefined);
    return obj;
  },

  fromPartial<I extends Exact<DeepPartial<Lures>, I>>(object: I): Lures {
    const message = createBaseLures();
    message.locations = object.locations?.map((e) => Geocircle.fromPartial(e)) || [];
    message.activeLure =
      object.activeLure !== undefined && object.activeLure !== null
        ? Geocircle.fromPartial(object.activeLure)
        : undefined;
    message.closestLure =
      object.closestLure !== undefined && object.closestLure !== null
        ? Geocircle.fromPartial(object.closestLure)
        : undefined;
    return message;
  },
};

type Builtin = Date | Function | Uint8Array | string | number | boolean | undefined;

export type DeepPartial<T> = T extends Builtin
  ? T
  : T extends Array<infer U>
  ? Array<DeepPartial<U>>
  : T extends ReadonlyArray<infer U>
  ? ReadonlyArray<DeepPartial<U>>
  : T extends { $case: string }
  ? { [K in keyof Omit<T, "$case">]?: DeepPartial<T[K]> } & { $case: T["$case"] }
  : T extends {}
  ? { [K in keyof T]?: DeepPartial<T[K]> }
  : Partial<T>;

type KeysOfUnion<T> = T extends T ? keyof T : never;
export type Exact<P, I extends P> = P extends Builtin
  ? P
  : P & { [K in keyof P]: Exact<P[K], I[K]> } & Record<Exclude<keyof I, KeysOfUnion<P>>, never>;

// If you get a compile-error about 'Constructor<Long> and ... have no overlap',
// add '--ts_proto_opt=esModuleInterop=true' as a flag when calling 'protoc'.
if (util.Long !== Long) {
  util.Long = Long as any;
  configure();
}

function isSet(value: any): boolean {
  return value !== null && value !== undefined;
}
