import { EstimatedLensPerformance } from "./benchmark/estimateLensPerformanceCluster";
import { copyDefinedProperties } from "./common/copyDefinedProperties";
import { Injectable } from "./dependency-injection/Injectable";

declare global {
    interface Window {
        /**
         * A property to be assigned with a custom WASM URL.
         */
        [SNAP_CAMKIT_DEBUG_KEY]: string | null;
    }
}

/**
 * From T, pick the set of properties whose values are optional. Create a new type containing only those properties.
 */
type PickOptionals<T> = {
    [K in keyof T as T[K] extends Exclude<T[K], undefined> ? never : K]: T[K];
};

/**
 * Defaults are provided for runtime configuration and any optional bootstrap configuration properties which require
 * defaults.
 */
const defaultConfiguration: CameraKitRuntimeConfiguration & PickOptionals<CameraKitBootstrapConfiguration> = {
    shouldUseWorker: true,
};

interface CameraKitRuntimeConfiguration {
    shouldUseWorker: boolean;
}

/**
 * A key on window object to provide custom WASM build.
 */
const SNAP_CAMKIT_DEBUG_KEY = "__snap_camkit_debug";

if (typeof window !== "undefined") {
    Object.defineProperty(window, SNAP_CAMKIT_DEBUG_KEY, {
        get() {
            return sessionStorage.getItem(SNAP_CAMKIT_DEBUG_KEY);
        },
        set(value: string) {
            return sessionStorage.setItem(SNAP_CAMKIT_DEBUG_KEY, value);
        },
        enumerable: false,
        configurable: false,
    });
}

/**
 * Checks whether there are configuration overrides defined, and if yes, returns them.
 * @returns Configuration overrides.
 */
function getConfigurationOverrides(): Partial<CameraKitBootstrapConfiguration> {
    if (!hasConfigurationOverrides()) return {};

    // Safety: hasConfigurationOverrides above ensures wasmEndpointOverride is non-null
    const wasmEndpointOverride = window[SNAP_CAMKIT_DEBUG_KEY]!;

    // if custom WASM is provided, we switch to debug mode
    const overrides = {
        wasmEndpointOverride,
        logger: "console" as const,
    };

    // always leave debug mode warning in console
    console.warn("Configuration overrides applied", overrides);
    return overrides;
}

/**
 * Returns true when configuration overrides for debug purposes are applied.
 */
export function hasConfigurationOverrides() {
    if (typeof window === "undefined") return false;

    const wasmEndpointOverride = window[SNAP_CAMKIT_DEBUG_KEY];
    return !!wasmEndpointOverride;
}

/**
 * Configuration which must be provided when calling {@link bootstrapCameraKit}. These values are used to create various
 * CameraKit components.
 *
 * @property
 */
export interface CameraKitBootstrapConfiguration {
    /**
     * Long-lived token granting your application access to CameraKit APIs. This is found in the SnapKit Dev Portal,
     * where it's called the API Token.
     */
    apiToken: string;

    /**
     * In recommended production deployments, the WebAssembly assets required by CameraKit will be downloaded from an
     * optimized CDN. But sometimes during development or within a CI pipeline, it may be necessary to download these
     * assets from somewhere else.
     *
     * @deprecated Please use `wasmEndpointOverride` instead. This property will be removed in the next major version.
     */
    wasmEndpoint?: string | null;

    /**
     * In recommended production deployments, the WebAssembly assets required by CameraKit will be downloaded from an
     * optimized CDN. But sometimes during development or within a CI pipeline, it may be necessary to download these
     * assets from somewhere else.
     */
    wasmEndpointOverride?: string;

    /**
     * Where to output log messages. By default, log messages are ignored.
     *
     * CameraKit emits multiple log messages, consumer can control where to output them:
     *  - "noop" or no value - log messages are ignored,
     *  - "console" - log messages are printed to console
     */
    logger?: "noop" | "console";

    /**
     * Some lenses may decide to modify their behavior based on the performance of the current environment. If you are
     * using such lenses, providing an estimation of lens performance may lead to better user experience (especially on
     * low-performance devices).
     *
     * Running the `estimateLensPerformance()` function will run benchmarks and estimate an appropriate lens performance
     * cluster (i.e. a performance rating) based on the current environment.
     *
     * Lower cluster = worse expected performance capability.
     */
    lensPerformance?: EstimatedLensPerformance | Promise<EstimatedLensPerformance>;
}

/**
 * In addition to the configuration required at bootstrap time, there are other configuration values that may be used
 * by CameraKit during its operation.
 */
export type CameraKitConfiguration = CameraKitRuntimeConfiguration & CameraKitBootstrapConfiguration;

export const configurationToken = "configuration";

export const createCameraKitConfigurationFactory = (configuration: CameraKitBootstrapConfiguration) =>
    Injectable(
        configurationToken,
        (): CameraKitConfiguration => ({
            ...defaultConfiguration,
            ...copyDefinedProperties(configuration),
            ...getConfigurationOverrides(),
        })
    );
