import {useEffect} from "react";
import {Ruid_Type as RuidType} from "../generated/common/ruid";
import {batchUpdateAbExposure, BatchUpdateAbExposureRequest, IdType, LoggingType} from "../grpc-wrappers";
import {
  CofConfig,
  useConfigFromContext, useCustomExposureLogger,
  useIsExposureLoggingDisabled,
  useOverride,
} from "./context";
import {CustomExposureLogger} from "./ConfigProvider";
import {DeepPartial} from "../generated/ab_service/exposure_service";

function isConfigAnExperiment(
  config: { studyName?: string; experimentId?: number } | null | undefined
): config is CofConfig {
  return Boolean(config && config.studyName && config.experimentId);
}

function isRuidMissing(config: CofConfig): boolean {
  // 0 is a valid ruidType => RuidType.UNSET, empty string is not a valid ruidValue
  return config.ruidType == null || !config.ruidValue;
}

function convertRuidToIdType(ruidType: RuidType): IdType {
  switch (ruidType) {
    case RuidType.AD_ACCOUNT:
      return IdType.AD_ACCOUNT;
    case RuidType.AD_MODERATION_AD:
    case RuidType.AM_MEMBER:
      return IdType.AM_MEMBER;
    case RuidType.AM_CLIENT:
      return IdType.AM_CLIENT;
    case RuidType.AM_ORGANIZATION:
      return IdType.AM_ORGANIZATION;
    case RuidType.AM_PROFILE:
      return IdType.AM_PROFILE;
    case RuidType.AM_SESSION:
      return IdType.AM_SESSION;
    case RuidType.AM_SNAPCHAT_USER:
      return IdType.AM_SNAPCHAT_USER;
    case RuidType.SNAPCHAT_ADVERTISING:
      return IdType.SNAPCHAT_ADVERTISING;
    case RuidType.USER:
    case RuidType.USERNAME:
      return IdType.WEB_SNAPCHAT_USER;
    case RuidType.WEB_CLIENT:
      return IdType.WEB_CLIENT;
    case RuidType.WEB_SNAPCHAT_USER:
      return IdType.WEB_SNAPCHAT_USER;
    case RuidType.DEVICE:
    case RuidType.INTERNAL:
    case RuidType.UNRECOGNIZED:
    case RuidType.UNSET:
      return IdType.UNRECOGNIZED;
    default: {
      // eslint-disable-next-line @typescript-eslint/no-unused-vars
      const ruid: never = ruidType; // used for exhaustive typechecking
      return IdType.UNRECOGNIZED;
    }
  }
}

export type ConfigValue = CofConfig["value"];

export const useConfigValue = (configId: string): ConfigValue | undefined => {
  const override = useOverride(configId);
  const config = useConfigFromContext(configId);
  const isExposureLoggingDisabled = useIsExposureLoggingDisabled();
  const customExposureLogger = useCustomExposureLogger();

  useEffect(() => {
    if (
      override ||
      isExposureLoggingDisabled ||
      !isConfigAnExperiment(config) ||
      isRuidMissing(config)
    ) {
      // skip logging the exposure
      return;
    }

    const request: DeepPartial<BatchUpdateAbExposureRequest> = {
      abExposureData: [
        {
          id: {
            type: convertRuidToIdType(config.ruidType),
            value: config.ruidValue,
          },
          properties: {
            userAgentString: navigator.userAgent,
          },
          loggingType: LoggingType.CLIENT_TRIGGER,
          studyTreatmentInfo: [
            { studyName: config.studyName, treatmentId: config.experimentId },
          ],
        },
      ],
    };

    if (customExposureLogger === CustomExposureLogger.CONSOLE) {
      console.log(`Logging exposure event to console ${JSON.stringify(request)}`)
    }

    batchUpdateAbExposure(request);
  }, [override, config, isExposureLoggingDisabled, customExposureLogger]);

  // TODO: improve the useOverride hook to recognize multiple data types and map then to the Values object
  if (override) {
    return { stringValue: override };
  }

  return config?.value;
};
