import { namedError } from "./common/errors";

const platformNotSupported = namedError("PlatformNotSupported");

/**
 * Error thrown when current platform is not supported by CameraKit.
 */
export type PlatformNotSupportedError = ReturnType<typeof platformNotSupported>;

/**
 * Assert current platform is supported.
 */
export function assertPlatformSupported() {
    const canvas = document.createElement("canvas");
    if (!canvas.getContext("webgl2") && !canvas.getContext("webgl")) {
        throw platformNotSupported(
            new Error("Cannot bootstrap the SDK because the browser does not support WebGL canvas rendering context.")
        );
    }
}
