import {
    isArrayBuffer,
    isArrayOfType,
    isFunction,
    isNumber,
    isRecord,
    isString,
    isTypedArray,
    isUndefined,
    predicateRecordValues,
} from "../common/typeguards";
import { Injectable } from "../dependency-injection/Injectable";
import { LensCoreModule, Uri, UriCancelRequest, UriRequest, UriResponse } from "../lens-core-module/generated-types";
import { Lens, toPublicLens } from "../lens/Lens";
import { LensContext } from "../session/LensContext";

const SEPARATOR = "://";

function extractSchemeAndRoute(uri: Uri) {
    const separatorIndex = uri.indexOf(SEPARATOR);
    const scheme = uri.slice(0, separatorIndex);
    const route = uri.slice(separatorIndex + SEPARATOR.length);
    return [scheme, route];
}

function isUri(value: unknown): value is Uri {
    return isString(value) && value.includes(SEPARATOR);
}

function isUriHandler(value: unknown): value is UriHandler {
    return (
        isRecord(value) &&
        (isUri(value.uri) || isArrayOfType(isUri, value.uri)) &&
        isFunction(value.handleRequest) &&
        (isUndefined(value.cancelRequest) || isFunction(value.cancelRequest))
    );
}

function isUriHandlers(value: unknown): value is UriHandlers {
    return isArrayOfType(isUriHandler, value);
}

function isUriResponse(value: unknown): value is UriResponse {
    return (
        isRecord(value) &&
        isNumber(value.code) &&
        isString(value.description) &&
        isString(value.contentType) &&
        (isArrayBuffer(value.data) || isTypedArray(value.data)) &&
        (isUndefined(value.metadata) || predicateRecordValues(isString)(value.metadata))
    );
}

/**
 * Provides a way for a lens to call into external services that work under HTTP-like protocol.
 */
export interface UriHandler {
    /**
     * [scheme]://[route] pattern that this handler processes or an array of patterns.
     */
    uri: Uri | Uri[];

    /**
     * Called to process the provided request.
     * @param request Lens request.
     * @param reply A callback function to send response back.
     * The function can be called none or multiple number of times.
     * @param lens Lens the request is coming from.
     */
    handleRequest(request: UriRequest, reply: (response: UriResponse) => void, lens: Lens): void;

    /**
     * Called to indicated that URI request initated earlier is cancelled.
     * @param request Lens request.
     * @param lens Lens the request is coming from.
     */
    cancelRequest?(request: UriCancelRequest, lens: Lens): void;
}

/**
 * Array of {@link UriHandler} objects.
 */
export type UriHandlers = UriHandler[];

/**
 * An extension point for client URI handlers.
 */
export const uriHandlersFactory = Injectable("UriHandlers", () => {
    const uriHandlers: UriHandlers = [];
    return uriHandlers;
});

/**
 * Registers URI handlers within LensCore.
 * @internal
 */
export function registerUriHandlers(lensCore: LensCoreModule, uriHandlers: UriHandlers, lensContext: LensContext) {
    if (!isUriHandlers(uriHandlers)) {
        throw new Error("Expected an array of UriHandler objects");
    }

    for (const { uri, handleRequest, cancelRequest } of uriHandlers) {
        const uris = Array.isArray(uri) ? uri : [uri];
        for (const [scheme, route] of uris.map(extractSchemeAndRoute)) {
            lensCore.registerUriListener(scheme, route, {
                handleRequest: (request) => {
                    const reply = (response: UriResponse) => {
                        if (!isUriResponse(response)) {
                            throw new Error("Expected UriResponse object");
                        }
                        lensCore.provideUriResponse(request.identifier, response);
                    };

                    // NOTE: we do not handle any error thrown on an extension side when handleRequest() is called.
                    // That responsibility is delegated to the extension by design and
                    // that is exactly what Android and iOS SDKs do.
                    // NOTE: it is expected that active lens is defined at this moment,
                    // because this handler is triggered by a lens
                    handleRequest(request, reply, toPublicLens(lensContext.activeLens!));
                },
                // NOTE: it is expected that active lens is defined at this moment,
                // because this handler is triggered by a lens
                cancelRequest: (request) => {
                    if (cancelRequest) {
                        cancelRequest(request, toPublicLens(lensContext.activeLens!));
                    }
                },
            });
        }
    }
}

export { Uri, UriCancelRequest, UriRequest, UriResponse } from "../lens-core-module/generated-types";
