import { createContext, useContext } from "react";
import type { Value } from "../generated/common/value";
import type {
  ConfigResult,
  ClientTargetingExpression,
} from "../generated/cdp/cof/config_result";
import { getEvaluatedCofConfig } from "../targeting_evaluation/evaluator";
import { CustomExposureLogger } from "./ConfigProvider";

export type CofConfig = Pick<
  ConfigResult,
  "configId" | "experimentId" | "priority" | "ruidType" | "studyName"
> & {
  value: Partial<Value>;
  ruidValue?: string;
  targetingExpression?: ClientTargetingExpression;
};

export type NormalizedConfigs = Record<string, CofConfig[]>;

type ConfigContextValue = {
  configs: NormalizedConfigs;
  overrides: Map<string, string>;
  isExposureLoggingDisabled: boolean;
  customExposureLogger: CustomExposureLogger;
};

const ConfigContext = createContext<ConfigContextValue | null>(null);

export const ConfigContextProvider = ConfigContext.Provider;

export const useConfigFromContext = (
  configId: string
): CofConfig | undefined => {
  const context = useContext(ConfigContext);

  if (!context) {
    throw new Error(
      "Context is not set. Please add a ConfigProvider in your app"
    );
  }

  const potentialConfigs = context.configs[configId];

  return potentialConfigs
    ? (getEvaluatedCofConfig(potentialConfigs, configId) as
        | CofConfig
        | undefined)
    : undefined;
};

export const useOverride = (configId: string): string | undefined => {
  const context = useContext(ConfigContext);
  if (!context) {
    throw new Error(
      "Context is not set. Please add a ConfigProvider in your app"
    );
  }

  return context.overrides.get(configId);
};

export const useIsExposureLoggingDisabled = (): boolean => {
  const context = useContext(ConfigContext);
  if (!context) {
    throw new Error(
      "Context is not set. Please add a ConfigProvider in your app"
    );
  }

  return context.isExposureLoggingDisabled;
};

export const useCustomExposureLogger = (): CustomExposureLogger => {
  const context = useContext(ConfigContext);
  if (!context) {
    throw new Error(
      "Context is not set. Please add a ConfigProvider in your app"
    );
  }

  return context.customExposureLogger;
};
