import { Injectable } from "../../dependency-injection/Injectable";
import { createArrayBufferParsingHandler } from "../../handlers/arrayBufferParsingHandler";
import { defaultFetchHandlerFactory, FetchHandler } from "../../handlers/defaultFetchHandler";
import { HandlerChainBuilder } from "../../handlers/HandlerChainBuilder";
import { LensRepository, lensRepositoryFactory } from "../LensRepository";
import { AssetLoader } from "./LensAssetsProvider";

/**
 * @internal
 */
export const staticAssetLoaderFactory = Injectable(
    "staticAssetLoader",
    [defaultFetchHandlerFactory.token, lensRepositoryFactory.token] as const,
    (fetchHandler: FetchHandler, lensRepository: LensRepository): AssetLoader => {
        const handler = new HandlerChainBuilder(fetchHandler).map(createArrayBufferParsingHandler()).handler;

        return async (asset, lens) => {
            const lensContent = lensRepository.getCachedLens(lens.id)?.content;
            const assetUrl = lensContent?.assetManifest.find((manifest) => manifest.id === asset.assetId)?.assetUrl;
            if (!assetUrl) {
                throw new Error(`Cannot load lens asset ${asset.assetId}. Static asset URL could not be found.`);
            }
            const [buffer, response] = await handler(assetUrl);
            if (!response.ok) throw response;
            return buffer;
        };
    }
);
