import { assertUnreachable } from "../../common/assertions";
import { getTimeMs } from "../../common/time";
import { Injectable } from "../../dependency-injection/Injectable";
import { scan } from "../../events/scan";
import { TypedCustomEvent } from "../../events/TypedCustomEvent";
import { SessionStateEventTarget, sessionStateEventTargetFactory } from "../../session/sessionStateEventTarget";
import { MakeTaggedBusinessEvent } from "../businessEventsReporter";
import { MetricsEventTarget, metricsEventTargetFactory } from "../metricsEventTarget";
import { operationalMetricReporterFactory, OperationalMetricsReporter } from "../operationalMetricsReporter";

interface NotStarted {
    name: "NotStarted";
}

interface Waiting {
    name: "Waiting";
    lensId: string;
    waitingStartTimeMs: number;
}

interface Complete {
    name: "Complete";
    lensId: string;
    waitingDurationMs: number;
}

type LensWaitState = NotStarted | Waiting | Complete;

// We ignore short-duration lens waits.
//
// The value is documented here:
// https://docs.google.com/document/d/1-kSzFWCWw9Qo3D08FR1_cqeHTsUtk9p3p3uOptzWDTY/edit#heading=h.q5liip76r9lt
const viewTimeThresholdSec = 0.1;

/**
 * The LensWait metric measures the time spent downloading the lens content and required assets. It gives an indication
 * of the real UX impact of download latency. If lens content and assets are pre-loaded, the latency measured here
 * should decrease  we only measure between the request to apply a lens and when the lens is ready to render.
 *
 * It corresponds to the internal CameraKitLensSpin event, described here:
 * https://docs.google.com/document/d/1-kSzFWCWw9Qo3D08FR1_cqeHTsUtk9p3p3uOptzWDTY#heading=h.q5liip76r9lt
 */
export type LensWait = MakeTaggedBusinessEvent<"lensWait">;

/**
 * Each time a lens is applied, we measure the duration until the lens is fully loaded by LensCore. This
 * includes any time spent downloading the lens content and required assets from the lens manifest.
 *
 * The intention of this event is to measure the experienced UX latency between a user requesting a lens and
 * the lens rendering. Of course, the application may call `applyLens` at any time, and may hide/show the
 * rendered result at any time  but this should give us a good baseline for how much UX latency could be seen.
 *
 * @internal
 */
export const reportLensWait = Injectable(
    "reportLensWait",
    [
        sessionStateEventTargetFactory.token,
        metricsEventTargetFactory.token,
        operationalMetricReporterFactory.token,
    ] as const,
    (
        stateTarget: SessionStateEventTarget,
        metricsEventTarget: MetricsEventTarget,
        reporter: OperationalMetricsReporter
    ) => {
        scan<LensWaitState>({ name: "NotStarted" })(
            stateTarget,
            ["lensApplyStart", "firstFrameProcessed"],
            (state, { type: eventType, detail: lens }) => {
                const complete = (state: Waiting): Complete => ({
                    name: "Complete",
                    lensId: state.lensId,
                    waitingDurationMs: getTimeMs() - state.waitingStartTimeMs,
                });

                const waiting = (): Waiting => ({
                    name: "Waiting",
                    lensId: lens.id,
                    waitingStartTimeMs: getTimeMs(),
                });

                switch (state.name) {
                    case "NotStarted":
                    case "Complete":
                        switch (eventType) {
                            case "lensApplyStart":
                                return waiting();

                            // This is nonsensical  we should never get a lensLoaded event without first getting a
                            // lensDownloadStart. If we do, we'll ignore it.
                            case "firstFrameProcessed":
                                return state;
                            default:
                                assertUnreachable(eventType);
                        }
                    case "Waiting":
                        switch (eventType) {
                            // If, while waiting, we get another lensApplyStart, this indicates that the prior lens
                            // is no longer being waited upon  so we'll complete it.
                            case "lensApplyStart":
                                recordEvent(complete(state));
                                return waiting();
                            case "firstFrameProcessed":
                                return complete(state);
                            default:
                                assertUnreachable(eventType);
                        }
                    default:
                        assertUnreachable(state);
                }
            }
        ).addEventListener("state", ({ detail }) => recordEvent(detail));

        const recordEvent = (state: LensWaitState) => {
            if (state.name !== "Complete") return;

            const viewTimeSec = state.waitingDurationMs / 1000;
            if (viewTimeSec < viewTimeThresholdSec) return;

            const lensWait: LensWait = {
                name: "lensWait",
                lensId: state.lensId,
                viewTimeSec,
            };
            metricsEventTarget.dispatchEvent(new TypedCustomEvent("lensWait", lensWait));
            reporter.timer("lens.apply_lens_latency", state.waitingDurationMs);
        };
    }
);
