import {
  ConfigTargetingRequest,
  DeepPartial,
} from "../generated/cdp/cof/config_request";
import { ConfigTargetingResponse } from "../generated/cdp/cof/config_response";
import type {
  ClientTargetingExpression,
  ConfigResult,
} from "../generated/cdp/cof/config_result";
import {
  ClientTargetingExpression_Operator as ClientTargetingExpressionOperator,
  ClientTargetingExpression_PredicateOperator as ClientTargetingExpressionPredicateOperator,
  ClientTargetingExpression_Property as ClientTargetingExpressionProperty,
  ConfigResult_ServePlatform as ConfigResultServePlatform,
} from "../generated/cdp/cof/config_result";
import { Namespace } from "../generated/cdp/cof/namespace";
import { Ruid_Type as RuidType } from "../generated/common/ruid";
import type { Value } from "../generated/common/value";
import type { UnaryMethodDefinitionish, Metadata } from "./grpcWeb";
import { getContextValidated } from "./initConfigsClient";

export {
  ConfigTargetingRequest,
  ConfigTargetingResponse,
  Namespace,
  RuidType,
  ConfigResult,
  BenchmarkRequest,
  BenchmarkName,
  Value,
  ClientTargetingExpression,
  ClientTargetingExpressionOperator,
  ClientTargetingExpressionProperty,
  ClientTargetingExpressionPredicateOperator,
  ConfigResultServePlatform,
};

export const CircumstancesServiceTargetingQueryDesc: UnaryMethodDefinitionish =
  {
    methodName: "targetingQuery",
    service: {
      serviceName: "snapchat.cdp.cof.CircumstancesService",
    },
    requestStream: false,
    responseStream: false,
    requestType: {
      serializeBinary() {
        return ConfigTargetingRequest.encode(this).finish();
      },
      // eslint-disable-next-line @typescript-eslint/no-explicit-any
    } as any,
    responseType: {
      deserializeBinary(data: Uint8Array) {
        return {
          ...ConfigTargetingResponse.decode(data),
          toObject() {
            return this;
          },
        };
      },
      // eslint-disable-next-line @typescript-eslint/no-explicit-any
    } as any,
  };

/**
 * Fetches COF configs.
 * Wrapper around https://github.sc-corp.net/Snapchat/pb_schema/blob/507ec1a8029de217836e29778ddd95ab8b5b8de4/proto/cdp/cof/circumstance_service.proto#L18
 **/
export function targetingQuery(
  request: DeepPartial<ConfigTargetingRequest>,
  metadata?: Metadata
): Promise<ConfigTargetingResponse> {
  const { rpc } = getContextValidated();
  return rpc.unary(
    CircumstancesServiceTargetingQueryDesc,
    ConfigTargetingRequest.fromPartial(request),
    metadata
  );
}
