import {
  ClientTargetingExpression,
  ClientTargetingExpression_Operator,
  ClientTargetingExpression_PredicateOperator,
  ClientTargetingExpression_Property,
  ConfigResult,
} from "../generated/cdp/cof/config_result";
import { isDefaultTargetingExpression } from "./util";

export type TargetingPropertyValueProvider = () => number | boolean | string;

/**
 * Map for developer-provided callbacks on how to retrieve values for client targeting properties.
 */
const targetingPropertyValueProviders: Map<
  ClientTargetingExpression_Property,
  TargetingPropertyValueProvider
> = new Map();

/**
 * Registers providers for targeting property values. These providers will be used when evaluating config rules
 * using client-side targeting expressions.
 */
export function registerPropertyValueProvider(
  targetingProperty: ClientTargetingExpression_Property,
  provider: TargetingPropertyValueProvider
): void {
  targetingPropertyValueProviders.set(targetingProperty, provider);
}

/**
 * A ConfigResult-like object used to wrap ConfigResult proto objects or CofConfig objects that the React hooks
 * in this SDK use. Makes use of Typescript duck-typing to allow extra fields not present on the object.
 */
export type ConfigResultWrapper = Pick<
  ConfigResult,
  "configId" | "priority"
> & { targetingExpression?: ClientTargetingExpression };

/**
 * Takes a list of config rules and returns the highest priority one that matches the configId and that has a
 * satisfied targeting expression. If a provided config rule does not have a targeting expression, that means that
 * the targeting expression was evaluated server-side and already deemed to be satisfied. If no config rules
 * have targeting expressions that are satisfied or no config rule with the config name was returned, undefined is
 * returned.
 * @param configResults - List of ConfigResult-like objects (only configId and targetingExpression must be present)
 * @param configName
 */
export function getEvaluatedCofConfig(
  configResults: ConfigResultWrapper[],
  configName: string
): ConfigResultWrapper | undefined {
  const configRulesForConfigId = configResults.filter(
    (configRule) => configRule.configId === configName
  );
  const noExpressionConfigRules = configRulesForConfigId.filter(
    (configRule) =>
      // Depending on the language for the generated proto code, targetingExpression may either be undefined or the
      // default proto message.
      !configRule.targetingExpression ||
      (configRule.targetingExpression &&
        isDefaultTargetingExpression(configRule.targetingExpression))
  );
  const clientTargetingExpressionConfigRules = configRulesForConfigId.filter(
    (configRule) =>
      configRule.targetingExpression &&
      !isDefaultTargetingExpression(configRule.targetingExpression)
  );

  const satisfiedClientConfigs = clientTargetingExpressionConfigRules.filter(
    (configRule) =>
      isTargetingExpressionSatisfied(
        configRule.targetingExpression as ClientTargetingExpression
      )
  );

  const sortedConfigs = [
    ...satisfiedClientConfigs,
    ...noExpressionConfigRules,
  ].sort((a, b) => {
    return b.priority - a.priority;
  });

  return sortedConfigs.length ? sortedConfigs[0] : undefined;
}

function isTargetingExpressionSatisfied(
  targetingExpression: ClientTargetingExpression
): boolean {
  if (targetingExpression.children.length > 0) {
    if (
      targetingExpression.operator === ClientTargetingExpression_Operator.AND
    ) {
      return targetingExpression.children.every((childExpression) =>
        isTargetingExpressionSatisfied(childExpression)
      );
    } else if (
      targetingExpression.operator === ClientTargetingExpression_Operator.OR
    ) {
      return targetingExpression.children.some((childExpression) =>
        isTargetingExpressionSatisfied(childExpression)
      );
    } else {
      return false;
    }
  }
  return evaluateLeafNode(targetingExpression);
}

function evaluateLeafNode(leaf: ClientTargetingExpression): boolean {
  // If it's not actually a leaf node for whatever reason, consider the evaluation result to be false.
  if (
    !leaf.property ||
    !leaf.value ||
    !leaf.predicateOperator ||
    leaf.children.length > 0
  ) {
    return false;
  }

  const propertyValueProvider = targetingPropertyValueProviders.get(
    leaf.property
  );

  if (!propertyValueProvider) {
    return false;
  }

  const propertyValue = propertyValueProvider();

  const comparisonValue =
    leaf.value.boolValue ??
    leaf.value.doubleValue ??
    leaf.value.floatValue ??
    leaf.value.intPairValue ??
    leaf.value.intValue ??
    leaf.value.longValue ??
    leaf.value.stringValue;

  // >= or <= operators can only operate on numbers (or proto enums)
  if (
    leaf.predicateOperator ===
      ClientTargetingExpression_PredicateOperator.GREATER_THAN_OR_EQUAL_TO ||
    leaf.predicateOperator ===
      ClientTargetingExpression_PredicateOperator.LESS_THAN_OR_EQUAL_TO
  ) {
    if (typeof comparisonValue !== "number") {
      return false;
    }

    if (typeof propertyValue !== "number") {
      return false;
    }

    if (
      leaf.predicateOperator ===
      ClientTargetingExpression_PredicateOperator.LESS_THAN_OR_EQUAL_TO
    ) {
      return propertyValue <= comparisonValue;
    } else {
      return propertyValue >= comparisonValue;
    }
  }

  if (
    leaf.predicateOperator ===
    ClientTargetingExpression_PredicateOperator.NOT_EQUAL
  ) {
    return propertyValue !== comparisonValue;
  }

  if (
    leaf.predicateOperator === ClientTargetingExpression_PredicateOperator.EQUAL
  ) {
    return propertyValue === comparisonValue;
  }

  return false;
}
