import { wrapError } from "../../common/errors";
import { Injectable } from "../../dependency-injection/Injectable";
import { TypedCustomEvent } from "../../events/TypedCustomEvent";
import { CameraKitSession, cameraKitSessionFactory } from "../../session/CameraKitSession";
import { LensContext, lensContextFactory } from "../../session/LensContext";
import { MakeTaggedBusinessEvent } from "../businessEventsReporter";
import { MetricsEventTarget, metricsEventTargetFactory } from "../metricsEventTarget";
import { operationalMetricReporterFactory, OperationalMetricsReporter } from "../operationalMetricsReporter";

/**
 * The Exception metric reports every time we handle an exception.
 */
export type Exception = MakeTaggedBusinessEvent<"exception">;

/**
 * @internal
 */
export const reportException = Injectable(
    "reportException",
    [
        cameraKitSessionFactory.token,
        lensContextFactory.token,
        metricsEventTargetFactory.token,
        operationalMetricReporterFactory.token,
    ] as const,
    (
        session: CameraKitSession,
        lensContext: LensContext,
        metricsEventTarget: MetricsEventTarget,
        reporter: OperationalMetricsReporter
    ) => {
        session.events.addEventListener("error", (event) => {
            const error = event.detail;
            metricsEventTarget.dispatchEvent(
                new TypedCustomEvent("exception", {
                    name: "exception",
                    lensId: lensContext.activeLens?.id ?? "original",
                    type: "lens",
                    reason: (error.cause ? wrapError(error, error.cause) : error).stack ?? "",
                })
            );
            reporter.count("handled_exception", 1, new Map([["type", "lens"]]));
        });
    }
);
