import { ClientTargetingExpression_Property } from "../generated/cdp/cof/config_result";
import { registerPropertyValueProvider } from "../targeting_evaluation/evaluator";
import { GrpcWebImpl, Metadata } from "./grpcWeb";

// key to store library context on window object
const CONTEXT_KEY = Symbol("context key");

declare global {
  interface Window {
    [CONTEXT_KEY]: ConfigsClientContext | undefined;
  }
}

/**
 * Configs client settings object.
 */
export interface ConfigsClientSettings {
  // A server that hosts Cirumstances Engine:
  // https://switchboard.sc-corp.net/#/services/cof-engine
  // and A/B Exposure service:
  // https://switchboard.sc-corp.net/#/services/exposure-service
  host: string;

  // common headers to add to each gRPC call
  metadata?: Metadata;
}

/**
 * Context object that is created as result of initConfigsClient() call.
 */
export interface ConfigsClientContext {
  // settigns passed to initConfigsClient()
  settings: ConfigsClientSettings;

  // gRPC web client
  rpc: GrpcWebImpl;
}

// default settings
const DEFAULT_SETTINGS: ConfigsClientSettings = {
  host: "https://aws.api.snapchat.com",
};

// store settings on a global object to avoid issues,
// when for some reason this modules is loaded more than once
if (typeof window !== "undefined") {
  Object.defineProperty(window, CONTEXT_KEY, {
    enumerable: false,
    writable: true,
  });
}

/**
 * Stores settings in globally.
 * @internal
 */
export function setContext(settings: ConfigsClientContext): void {
  if (typeof window !== "undefined") {
    window[CONTEXT_KEY] = settings;
  }
}

/**
 * Reads previously stored settings.
 */
function getContext(): ConfigsClientContext | undefined {
  if (typeof window !== "undefined") {
    return window[CONTEXT_KEY];
  }
}

/**
 * Returns settings object previously initialized with initConfigsClient().
 * @internal
 */
export function getContextValidated(): ConfigsClientContext {
  const context = getContext();
  if (!context) {
    throw new Error("initConfigsClient() has to be called on app startup");
  }
  return context;
}

/**
 * Initializes COF configs client.
 * The function has to be called once on app startup.
 */
export function initConfigsClient(
  settings: ConfigsClientSettings = DEFAULT_SETTINGS
): void {
  const existingContext = getContext();
  if (existingContext) {
    // there is a chance that bundlers include initConfigsClient() into different chunks,
    // we are good if the same settigs object is passed,
    // otherwise throw an error;
    // JSON.stringify approach is simple and good to catch exactly that issue:
    // the same settings objects are created in the same way in different chunks
    if (JSON.stringify(settings) !== JSON.stringify(existingContext.settings)) {
      throw new Error("initConfigsClient() has already been called");
    }
  } else {
    setContext({
      settings,
      rpc: new GrpcWebImpl(settings.host, { metadata: settings.metadata }),
    });

    // Add default providers for browser height and width. Can be overrided by re-registering these properties.
    registerPropertyValueProvider(
      ClientTargetingExpression_Property.WEB_SCREEN_HEIGHT,
      () => window.innerHeight
    );
    registerPropertyValueProvider(
      ClientTargetingExpression_Property.WEB_SCREEN_WIDTH,
      () => window.innerWidth
    );
  }
}
