import { Injectable } from "../../dependency-injection/Injectable";
import { TypedCustomEvent } from "../../events/TypedCustomEvent";
import { SessionStateEventTarget, sessionStateEventTargetFactory } from "../../session/sessionStateEventTarget";
import { MakeTaggedBusinessEvent } from "../businessEventsReporter";
import { MetricsEventTarget, metricsEventTargetFactory } from "../metricsEventTarget";

/**
 * The AssetValidationFailed metric reports every time we handle an asset checksum validation failure.
 */
export type AssetValidationFailed = MakeTaggedBusinessEvent<"assetValidationFailed">;

/**
 * The LensContentValidationFailed metric reports every time we handle an lens checksum validation failure.
 */
export type LensContentValidationFailed = MakeTaggedBusinessEvent<"lensContentValidationFailed">;

/**
 * @internal
 */
export const reportLensOrAssetValidationFailed = Injectable(
    "reportLensOrAssetValidationFailed",
    [sessionStateEventTargetFactory.token, metricsEventTargetFactory.token] as const,
    (stateTarget: SessionStateEventTarget, metricsEventTarget: MetricsEventTarget) => {
        stateTarget.addEventListener("assetValidationFailure", ({ detail: assetDescriptor }) => {
            const assetValidationFailed: AssetValidationFailed = {
                name: "assetValidationFailed",
                assetId: assetDescriptor.assetId,
            };
            metricsEventTarget.dispatchEvent(new TypedCustomEvent("assetValidationFailed", assetValidationFailed));
        });
        stateTarget.addEventListener("lensContentValidationFailure", ({ detail: lens }) => {
            const lensContentValidationFailed: LensContentValidationFailed = {
                name: "lensContentValidationFailed",
                lensId: lens.id,
            };
            metricsEventTarget.dispatchEvent(
                new TypedCustomEvent("lensContentValidationFailed", lensContentValidationFailed)
            );
        });
    }
);
