/** Lenses service exposes methods related to the Lenses functionality */
export interface Lenses {
  /** GetGroup returns group according to GetGroupRequests parameters */
  GetGroup(request: GetGroupRequest): Promise<GetGroupResponse>;
  /** GetGroupLens returns lens ID in the context of provided Group ID */
  GetGroupLens(request: GetGroupLensRequest): Promise<GetGroupLensResponse>;
  /**
   * GetPlaceholderConfig returns config values where all values do not change depends on the request context
   * such as device type, device class, etc.
   */
  GetPlaceholderConfig(request: GetPlaceholderConfigRequest): Promise<GetPlaceholderConfigResponse>;
}

/** Metrics service exposes methods that relate to operational or business metrics */
export interface Metrics {
  /** SetOperationalMetrics sets multiple operational metrics */
  SetOperationalMetrics(request: SetOperationalMetricsRequest): Promise<SetOperationalMetricsResponse>;
  /** SetBusinessEvents sets multiple business events */
  SetBusinessEvents(request: SetBusinessEventsRequest): Promise<SetBusinessEventsResponse>;
  /** SetExtensionBusinessEvents sets multiple business events from SDK extensions */
  SetExtensionBusinessEvents(request: SetExtensionBusinessEventsRequest): Promise<SetExtensionBusinessEventsResponse>;
}

type Builtin = Date | Function | Uint8Array | string | number | boolean | undefined;

export type DeepPartial<T> = T extends Builtin
  ? T
  : T extends Array<infer U>
  ? Array<DeepPartial<U>>
  : T extends ReadonlyArray<infer U>
  ? ReadonlyArray<DeepPartial<U>>
  : T extends { $case: string }
  ? { [K in keyof Omit<T, "$case">]?: DeepPartial<T[K]> } & { $case: T["$case"] }
  : T extends {}
  ? { [K in keyof T]?: DeepPartial<T[K]> }
  : Partial<T>;

type KeysOfUnion<T> = T extends T ? keyof T : never;
export type Exact<P, I extends P> = P extends Builtin
  ? P
  : P & { [K in keyof P]: Exact<P[K], I[K]> } & Record<Exclude<keyof I, KeysOfUnion<P>>, never>;

// If you get a compile-error about 'Constructor<Long> and ... have no overlap',
// add '--ts_proto_opt=esModuleInterop=true' as a flag when calling 'protoc'.
if (util.Long !== Long) {
  util.Long = Long as any;
  configure();
}

function isObject(value: any): boolean {
  return typeof value === "object" && value !== null;
}

function isSet(value: any): boolean {
  return value !== null && value !== undefined;
}
