import React, { ReactElement, useMemo } from "react";
import { CofConfig, ConfigContextProvider, NormalizedConfigs } from "./context";

export type ConfigProviderProps = {
  configs: CofConfig[];
  children: ReactElement;
  disableExposureLogging?: boolean;
  customExposureLogger?: CustomExposureLogger;
  enableOverrides?: boolean;
};

/**
 * NOOP = default value doesn't do anything
 * CONSOLE = log to console, useful for debugging
 */
export enum CustomExposureLogger {
  NOOP,
  CONSOLE,
}

export const ConfigProvider = ({
  configs,
  children,
  disableExposureLogging = false,
  customExposureLogger = CustomExposureLogger.NOOP,
  enableOverrides,
}: ConfigProviderProps): ReactElement => {
  const normalizedConfigs = useMemo(() => {
    const configMap: NormalizedConfigs = {};

    configs.forEach((config) => {
      if (!configMap[config.configId]) {
        configMap[config.configId] = [config];
      } else {
        configMap[config.configId]!.push(config);
      }
    });

    return configMap;
  }, [configs]);

  const overrides = useMemo(() => {
    if (!enableOverrides) {
      return new Map<string, string>();
    }

    const queryParams = new URLSearchParams(window.location.search);
    const overrides = queryParams.get("overrides") ?? "";
    const overridePairs = overrides
      .split(",")
      .map((pairs) => pairs.split(":") as [string, string]);

    return new Map<string, string>(overridePairs);
  }, [enableOverrides, window.location.search]);

  return (
    <ConfigContextProvider
      value={{
        configs: normalizedConfigs,
        overrides,
        isExposureLoggingDisabled: disableExposureLogging,
        customExposureLogger,
      }}
    >
      {children}
    </ConfigContextProvider>
  );
};
