import { Injectable } from "../dependency-injection/Injectable";
import { TypedCustomEvent } from "../events/TypedCustomEvent";
import { TypedEventTarget } from "../events/TypedEventTarget";
import { Lens } from "../lens";
import { AssetDescriptor } from "../lens-core-module/types";

export type SessionLifecycleEvents =
    | TypedCustomEvent<"sessionCreated">
    | TypedCustomEvent<"sessionSuspended">
    | TypedCustomEvent<"sessionResumed">
    | TypedCustomEvent<"sessionDestroyed">;

export type SessionLensEvents =
    | TypedCustomEvent<"lensApplyStart", Lens>
    | TypedCustomEvent<"lensDownloadComplete", Lens>
    | TypedCustomEvent<"lensInitiated", Lens>
    | TypedCustomEvent<"lensTurnedOn", Lens>
    | TypedCustomEvent<"lensLoaded", Lens>
    | TypedCustomEvent<"firstFrameProcessed", Lens>
    | TypedCustomEvent<"lensTurnedOff", Lens>
    | TypedCustomEvent<"lensContentValidationFailure", Lens>;

export type SessionAssetEvents = TypedCustomEvent<"assetValidationFailure", AssetDescriptor>;

export type SessionStateEvents = SessionLensEvents | SessionAssetEvents | SessionLifecycleEvents;

/**
 * @internal
 */
export const sessionStateEventTargetFactory = Injectable(
    "sessionEventTarget",
    () => new TypedEventTarget<SessionStateEvents>()
);

export type SessionStateEventTarget = ReturnType<typeof sessionStateEventTargetFactory>;
