import { getLogger } from "../logger";
import { Persistence, ValidKey } from "../persistence/Persistence";
import { ChainableHandler, RequestMetadata } from "./HandlerChainBuilder";

const logger = getLogger("responseCacheHandler");

/**
 * Each cache entry is a tuple of [timestamp, response] values.
 */
type CacheEntry<T> = [number, T];

/**
 * Handler params.
 */
interface Params<Req, Res, Meta> {
    /**
     * Response cache implementation.
     */
    cache: Persistence<CacheEntry<Res>>;

    /**
     * A function to resolve a cache key.
     */
    keyResolver: (req: Req, metadata: Meta | undefined) => ValidKey;

    /**
     * Number of seconds a response remains fresh.
     */
    maxAge: number;
}

/**
 * The handler calls inner handler to retrieve a response. If that succeeds, it returns the response.
 * Otherwise it returns previosly stored response. If the previous response is not available,
 * the handler re-throws original error.
 *
 * That is stale-when-error caching strategy:
 * https://developer.chrome.com/docs/workbox/caching-strategies-overview/#network-first-falling-back-to-cache
 *
 * TODO: when revisiting caching approaches check this PR comment out:
 * https://github.sc-corp.net/Snapchat/camera-kit-web-sdk/pull/265#pullrequestreview-3793011
 *
 * @returns Received or cached response object.
 */
export const staleIfErrorCachingHandler = <Req, Res, Meta extends RequestMetadata>({
    cache,
    keyResolver,
    maxAge,
}: Params<Req, Res, Meta>): ChainableHandler<Req, Res, Req, Res, Meta> => {
    return (next) => async (req, metadata) => {
        let key: ValidKey;
        try {
            key = keyResolver(req, metadata);
        } catch (error) {
            logger.warn("Could not resolve cache key due to error", error);
            return next(req, metadata);
        }
        try {
            const response = await next(req, metadata);
            await cache
                .store(key, [Date.now(), response])
                .catch((err) => logger.warn(`Could not cache response with key ${key}`, err));
            logger.debug("Added/updated cache entry for key", key);
            return response;
        } catch (error) {
            logger.debug("Inner handler threw", error, "\nWill try to lookup cache...");
            const entry = await cache
                .retrieve(key)
                .catch((err) => logger.warn("Unable to retrieve response from cache", err));
            if (entry) {
                const [timestamp, response] = entry;
                if (timestamp + maxAge * 1000 > Date.now()) {
                    return response;
                }
                logger.debug("Stale cache entry for key", key, ", re-throwing original error...");
                throw error;
            }
            logger.debug("No cache entry for key", key, ", re-throwing original error...");
            throw error;
        }
    };
};
